.stats-tabs {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
  min-width: 80%;
  max-width: 90%;
  margin: 0;
  padding: 0;

  &__item {
    display: flex;
    position: relative;
    width: auto;
    margin: 0 6px;
    font-size: 10px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 1px;

    &:first-child {
      margin-left: 0;
    }

    &:last-child {
      margin-right: 0;
    }

    a {
      display: block;
      position: relative;
      color: #bbb;
      text-decoration: none;
      cursor: pointer;
      white-space: nowrap;

      &::after {
        display: block;
        position: absolute;
        width: 100%;
        height: 1px;
        bottom: 15px;
        left: 0;
      }

      &:hover {

        &::after {
          content: '';
          background-color: #1a1a1a;
        }
      }

      &.active {
        color: #000;

        &::after {
          content: '';
          background-color: #000;
        }
      }
    }
  }
}
