.zones {
  position: absolute;
  z-index: 999;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  width: 100%;
  height: 100%;
  flex-wrap: wrap;
  background: linear-gradient(to right, rgba(#fff, 0.6) 0%, rgba(#fff, 0.6) 50%, transparent 50%);
  border: 1px solid #339595;

  &__episode {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: 1px solid #339595;
    cursor: pointer;

    &:hover {
      background-color: rgba(#fff, 0.3);
    }

    p {
      margin: 0;
      font-size: 21px;
      line-height: 26px;
      font-weight: 700;
    }

    span {
      font-size: 14px;
    }

    &.left-1 {
      order: 9;
      width: 183.767px;
      height: 111.433px;
      border: none;
      border-right: 1px solid #339595;
    }
    &.left-2 {
      order: 10;
      height: 111.433px;
      width: 69.3667px;
      border: none;
      border-right: 1px solid #339595;
    }
    &.left-3 {
      order: 6;
      height: 70.5167px;
      width: 69.3667px;
      border-top: none;
      border-left: none;
    }
    &.left-4 {
      order: 2;
      height: 111.433px;
      width: 69.3667px;
      border-top: none;
      border-left: none;
    }
    &.left-5 {
      order: 1;
      width: 183.767px;
      height: 111.433px;
      border-left: none;
      border-top: none;
    }
    &.left-6 {
      order: 5;
      width: 183.767px;
      height: 70.5167px;
      border-left: none;
      border-top: none;
    }
    &.right-1 {
      order: 4;
      width: 183.767px;
      height: 111.433px;
      border-top: none;
      border-right: none;
    }
    &.right-2 {
      order: 3;
      height: 111.433px;
      width: 69.3667px;
      border-top: none;
      border-right: none;
    }
    &.right-3 {
      order: 7;
      height: 70.5167px;
      width: 69.3667px;
      border-top: none;
      border-right: none;
    }
    &.right-4 {
      order: 11;
      height: 111.433px;
      width: 69.3667px;
      border: none;
      border-left: 1px solid #339595;
    }
    &.right-5 {
      order: 12;
      width: 183.767px;
      height: 111.433px;
      border: none;
      border-left: 1px solid #339595;
    }
    &.right-6 {
      order: 8;
      width: 183.767px;
      height: 70.5167px;
      border-top: none;
      border-right: none;
    }
  }

  &_6 {
    .left-7,
    .left-8,
    .left-9,
    .right-7,
    .right-8,
    .right-9 {
      display: none;
    }
  }

  &_9 {
    .left-7,
    .left-8,
    .left-9,
    .right-7,
    .right-8,
    .right-9 {
      display: flex;
    }

    .left-1 {
      order: 13;
      width: 111.617px;
      height: 112.4px;
    }

    .left-2 {
      order: 14;
      width: 70.8px;
      height: 112.4px;
    }

    .left-3 {
      order: 15;
      width: 70.63px;
      height: 112.4px;
      border-bottom: none;
    }

    .left-4 {
      order: 7;
      width: 111.617px;
      height: 70.5167px;
    }

    .left-5 {
      order: 8;
      width: 70.8px;
      height: 70.5167px;
    }

    .left-6 {
      order: 9;
      width: 70.63px;
      height: 70.5167px;
    }

    .left-7 {
      order: 3;
      width: 70.63px;
      height: 111.433px;
      border-top: none;
      border-left: none;
    }

    .left-8 {
      order: 2;
      width: 70.8px;
      height: 111.433px;
      border-left: none;
      border-top: none;
    }

    .left-9 {
      order: 1;
      width: 111.617px;
      height: 111.433px;
      border-left: none;
      border-top: none;
    }

    .right-1 {
      order: 6;
      width: 112.3px;
      height: 111.433px;
    }

    .right-2 {
      order: 5;
      width: 71.1px;
      height: 111.433px;
    }

    .right-3 {
      order: 4;
      width: 69.55px;
      height: 111.433px;
    }

    .right-4 {
      order: 12;
      width: 112.3px;
      height: 70.5167px;
    }

    .right-5 {
      order: 11;
      width: 71.1px;
      height: 70.5167px;
    }

    .right-6 {
      order: 10;
      width: 69.55px;
      height: 70.5167px;
      border-bottom: none;
    }

    .right-7 {
      order: 17;
      width: 69.55px;
      height: 112.4px;
      border-right: none;
      border-bottom: none;
    }

    .right-8 {
      order: 17;
      width: 71.1px;
      height: 112.4px;
      border-right: none;
      border-bottom: none;
    }

    .right-9 {
      order: 17;
      width: 112.3px;
      height: 112.4px;
      border-right: none;
      border-bottom: none;
    }
  }

  &_6,
  &_9 {
    .right-1, .right-2,
    .right-3, .right-4,
    .right-5, .right-6,
    .right-7, .right-8,
    .right-9 {
      opacity: 0;
      pointer-events: none;
    }
  }

  &_right {
    background: linear-gradient(to left, rgba(#fff, 0.6) 0%, rgba(#fff, 0.6) 50%, transparent 50%);
    &.zones_6,
    &.zones_9 {
      .right-1, .right-2,
      .right-3, .right-4,
      .right-5, .right-6,
      .right-7, .right-8,
      .right-9 {
        opacity: 1;
        pointer-events: auto;
      }

      .left-1, .left-2,
      .left-3, .left-4,
      .left-5, .left-6,
      .left-7, .left-8,
      .left-9 {
        opacity: 0;
        pointer-events: none;
      }
    }
  }
}
