article.player, article.player.for-team{

	.player-card-wrapper{
		display: flex;
		justify-content: space-between;
	}

	.player-card{
	  background: white;
	  .player-column.player-column-avatar{
		  margin-bottom: 15px;
	    vertical-align: bottom;
	    float: none;
		  margin-right: 10px;
	  }
	  .player-column.player-column-name{
	  	max-width: none;
	  	float: none;
	  }
	  .player-card-avatar{
	  	padding: 0;
	  	width: 120px;
	  	img{
	  		width: 90px;
	  		height: auto;
			margin-bottom: 10px;
	  	}
	  	&:after{
	  		box-shadow: 0 -10px 25px 25px rgba(0, 0, 0, 0.3);
	  		top: 70%;
	  	}
	  }
	  .player-card-title{
	  	position: relative;
	  	width: 320px;
	  	margin-bottom: 5px;
        margin-top: 0;
	  }
	}

}

article.player.for-player {

  .player-card{

    .container-fluid {

      .player-column-avatar {
        display: flex;
        justify-content: center;
        align-items: center;
        vertical-align: unset;
        width: 110px;
        height: 110px;
        margin-bottom: 5px;
        margin-right: 20px;

        .player-card-avatar{
          border-radius: 50%;
          overflow: hidden;
          background-color: #F2F2F2;
          height: 110px;
          display: flex;
          justify-content: center;
          align-items: center;

          &_national {
            padding: 15px;
          }

          img{
            display: block;
            width: 100%;
            height: auto;
            margin-bottom: 20px;
          }
        }
      }
    }
  }
}


.player-card-labels{
	display: flex;
	align-items: center;
	position: absolute;
	clear: both;
	width: 100%;
	white-space: nowrap;
	margin-top: 10px;
}

.player-label{
	position: relative;
	line-height: 12px;
	margin-right: 5px;
	padding-right: 5px;
	display: inline-block;
	color: #999;
	cursor: pointer;

	&:hover {
		color: #1a1a1a;
	}

	> .flag-icon{
		vertical-align: middle;
	}

  .flag-icon.flag-icon-24x16 {
	  width: 24px;
	  height: 16px;
	  background-size: 360px;
  }

	&.css3-tooltip-trigger:after{
		margin-top: 12px;
	}
	&.css3-tooltip-trigger:before{
		margin-top: 2px;
	}
}
