.icon-person,
.icon-player,
.icon-team,
.icon-tournament {
  display: inline-block;
  height: 15px;
  width: 15px;
}

.icon-person,
.icon-player {
  background-image: url('/images/icons/svg/icon-search-player.svg');
  background-size: contain;
  background-repeat: no-repeat;
}

.icon-team {
  background-image: url('/images/icons/svg/icon-search-team.svg');
  background-size: contain;
}
.icon-tournament {
  background-image: url('/images/icons/svg/icon-search-tournaments.svg');
  background-size: contain;
}

.icon-profile {
  display: block;
  width: 18px;
  height: 18px;
  background-image: url(/images/icons/svg/icon-profile.svg);
  background-repeat: no-repeat;
  background-size: contain;
}

.icon-language {
  display: block;
  width: 18px;
  height: 18px;
