  background-image: url(/images/icons/svg/icon-search-lens.svg);
  background-repeat: no-repeat;
  background-size: contain;
}

.icon-play, 
.players-details-header-episodes-icon {
  position: relative;
  display: block;
  width: 16px;
  height: 16px;
  opacity: 0.5;
  background-image: url(/images/icons/svg/icon-play.svg);
  background-size: contain;
  background-repeat: no-repeat;
  &:hover {
    opacity: 1;
  }
}

