@mixin clearFix() {
    &:after {
        content: ".";
        display: block;
        height: 0px;
        clear: both;
        visibility: hidden;
    }
    .ie & {
        zoom: 1;
        display: block;
    }
}

@mixin userSelect($x) {
    -webkit-touch-callout: $x;
    -webkit-user-select: $x;
    -khtml-user-select: $x;
    -moz-user-select: $x;
    -ms-user-select: $x;
    user-select: $x;
}


@mixin opacity($x) {
    $x_ie: round($x * 100);
    filter: progid:DXImageTransform.Microsoft.Alpha(opacity=#{$x_ie});
    filter: alpha(opacity=#{$x_ie});
    opacity: $x;
}

@mixin boxShadowImage($w, $h) {
    box-shadow: 0 0 3px #b2b2b2;
    width: $w;
    height: $h;
    position: relative;
    &:before {
        box-shadow: inset 1px 0 0 #fff, inset 0 1px 0 #fff, inset -1px 0 0 #fff, inset 0 -1px 0 #fff;
        width: $w;
        height: $h;
        position: absolute;
        top: 0;
        left: 0;
        content: '';
    }
    img {
        max-width: 100%;
        max-height: 100%;
    }
}
@mixin boxShadowContainer() {
    border: 1px solid #e5e5e5;
    box-shadow: inset 1px 0 0 #fff, inset 0 1px 0 #fff, inset -1px 0 0 #fff, inset 0 -1px 0 #fff, 0 0 2px #e5e5e5;
}
@mixin separator($w, $h, $top) {
    position: relative;
    &:before {
        background: -moz-radial-gradient(right center, ellipse cover,  #d9d9d9 0%, #ffffff 70%);
        background: -webkit-gradient(radial, right center, 0px, center center, 100%, color-stop(0%,#d9d9d9), color-stop(70%,#ffffff));
        background: -webkit-radial-gradient(right center, ellipse cover,  #d9d9d9 0%,#ffffff 70%);
        background: -o-radial-gradient(right center, ellipse cover,  #d9d9d9 0%,#ffffff 70%);
        background: -ms-radial-gradient(right center, ellipse cover,  #d9d9d9 0%,#ffffff 70%);
        background: radial-gradient(ellipse at right center,  #d9d9d9 0%,#ffffff 70%);
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#d9d9d9', endColorstr='#ffffff',GradientType=1 );
        width: $w;
        height: $h;
        overflow: hidden;
        content: '';
        position: absolute;
        top: $top;
        left: -$w;
    }
}
@mixin separatorLeft($w, $h, $top) {
    position: relative;
    &:before {
        background: -moz-radial-gradient(left center, ellipse cover,  #d9d9d9 0%, #ffffff 70%);
        background: -webkit-gradient(radial, left center, 0px, center center, 100%, color-stop(0%,#d9d9d9), color-stop(70%,#ffffff));
        background: -webkit-radial-gradient(left center, ellipse cover,  #d9d9d9 0%,#ffffff 70%);
        background: -o-radial-gradient(left center, ellipse cover,  #d9d9d9 0%,#ffffff 70%);
        background: -ms-radial-gradient(left center, ellipse cover,  #d9d9d9 0%,#ffffff 70%);
        background: radial-gradient(ellipse at left center,  #d9d9d9 0%,#ffffff 70%);
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#d9d9d9', endColorstr='#ffffff',GradientType=1 );
        width: $w;
        height: $h;
        overflow: hidden;
        content: '';
        position: absolute;
        top: $top;
        right: -$w;
    }
}
@mixin z-index($x) {
    z-index: $x;
    position: relative;
}

// Form
@mixin textControl() {
    font-family: $baseFontFamily;
    color: #353535;
    font-size: round($baseFontSize * 1.167);
    vertical-align: middle;
    &::-moz-focus-inner {
        padding: 0;
        border: 0;
    }
}

@mixin placeholder() {
    &::-moz-placeholder {
        color: #868686;
        font-style: italic;
        font-family: $baseFontFamily;
        font-size: round($baseFontSize * 1.167);
    }
    &::-webkit-input-placeholder {
        font-style: italic;
        color: #868686;
        font-family: $baseFontFamily;
        font-size: round($baseFontSize * 1.167);
    }
    &:-ms-input-placeholder {
        font-style: italic;
        color: #868686;
        font-family: $baseFontFamily;
        font-size: round($baseFontSize * 1.167);
    }
}

@mixin buttonReset() {
    background: transparent;
    border-radius: 0px;
    border: 0px none;
    overflow: visible;
    margin: 0;
    padding: 0;
    cursor: pointer;
    vertical-align: middle;
    white-space: nowrap;
    &::-moz-focus-inner {
        padding: 0;
        border: 0;
    }
}

@mixin gradient() {
    background: #0264c4;
    background: -moz-linear-gradient(top,  #0264c4 0%, #015db6 25%, #0154a5 50%, #004d98 75%, #004990 100%);
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,#0264c4), color-stop(25%,#015db6), color-stop(50%,#0154a5), color-stop(75%,#004d98), color-stop(100%,#004990));
    background: -webkit-linear-gradient(top,  #0264c4 0%,#015db6 25%,#0154a5 50%,#004d98 75%,#004990 100%);
    background: -o-linear-gradient(top,  #0264c4 0%,#015db6 25%,#0154a5 50%,#004d98 75%,#004990 100%);
    background: -ms-linear-gradient(top,  #0264c4 0%,#015db6 25%,#0154a5 50%,#004d98 75%,#004990 100%);
    background: linear-gradient(to bottom,  #0264c4 0%,#015db6 25%,#0154a5 50%,#004d98 75%,#004990 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#0264c4', endColorstr='#004990',GradientType=0 );
}
@mixin gradientHover() {
    background: #004990;
    background: -moz-linear-gradient(top,  #004990 0%, #004d98 25%, #0154a5 50%, #015db6 75%, #0264c4 100%);
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,#004990), color-stop(25%,#004d98), color-stop(50%,#0154a5), color-stop(75%,#015db6), color-stop(100%,#0264c4));
    background: -webkit-linear-gradient(top,  #004990 0%,#004d98 25%,#0154a5 50%,#015db6 75%,#0264c4 100%);
    background: -o-linear-gradient(top,  #004990 0%,#004d98 25%,#0154a5 50%,#015db6 75%,#0264c4 100%);
    background: -ms-linear-gradient(top,  #004990 0%,#004d98 25%,#0154a5 50%,#015db6 75%,#0264c4 100%);
    background: linear-gradient(to bottom,  #004990 0%,#004d98 25%,#0154a5 50%,#015db6 75%,#0264c4 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#004990', endColorstr='#0264c4',GradientType=0 );
}
@mixin grayGradient() {
    background: #f7f7f7;
    background: -moz-linear-gradient(top,  #f7f7f7 0%, #e4e4e4 100%);
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,#f7f7f7), color-stop(100%,#e4e4e4));
    background: -webkit-linear-gradient(top,  #f7f7f7 0%,#e4e4e4 100%);
    background: -o-linear-gradient(top,  #f7f7f7 0%,#e4e4e4 100%);
    background: -ms-linear-gradient(top,  #f7f7f7 0%,#e4e4e4 100%);
    background: linear-gradient(to bottom,  #f7f7f7 0%,#e4e4e4 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f7f7f7', endColorstr='#e4e4e4',GradientType=0 );
}
@mixin grayGradientHover() {
    background: #e4e4e4;
    background: -moz-linear-gradient(top,  #e4e4e4 0%, #f7f7f7 100%);
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,#e4e4e4), color-stop(100%,#f7f7f7));
    background: -webkit-linear-gradient(top,  #e4e4e4 0%,#f7f7f7 100%);
    background: -o-linear-gradient(top,  #e4e4e4 0%,#f7f7f7 100%);
    background: -ms-linear-gradient(top,  #e4e4e4 0%,#f7f7f7 100%);
    background: linear-gradient(to bottom,  #e4e4e4 0%,#f7f7f7 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#e4e4e4', endColorstr='#f7f7f7',GradientType=0 );
}

@mixin linearGradient($start, $end) {
    background: $start;
    background: linear-gradient(to bottom, $start 0%, $end 100%);
    background: -webkit-linear-gradient(top, $start 0%, $end 100%);
    background: -o-linear-gradient(top, $start 0%, $end 100%);
    background: -moz-linear-gradient(top, $start 0%, $end 100%);
    background: -ms-linear-gradient(top, $start 0%, $end 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='$start', endColorstr='$end',GradientType=0 );
    -ms-filter: "progid:DXImageTransform.Microsoft.gradient( startColorstr=$start, endColorstr=$end,GradientType=0 )";
}

@mixin linearGradientHorizontal($start, $start_prc, $end, $end_prc) {
    background: $start;
    background: linear-gradient(to right, $start $start_prc, $end $end_prc);
    background: -webkit-linear-gradient(left, $start $start_prc, $end $end_prc);
    background: -o-linear-gradient(left, $start $start_prc, $end $end_prc);
    background: -moz-linear-gradient(left, $start $start_prc, $end $end_prc);
    background: -ms-linear-gradient(left, $start $start_prc, $end $end_prc);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='$start', endColorstr='$end',GradientType=1 );
    -ms-filter: "progid:DXImageTransform.Microsoft.gradient( startColorstr=$start, endColorstr=$end,GradientType=1 )";
}

@mixin linearGradientVertical($start, $start_prc, $end, $end_prc) {
    background: $start; /* Old browsers */
    background: -moz-linear-gradient(top, $start $start_prc, $end $end_prc); /* FF3.6+ */
    background: -webkit-gradient(linear, left top, left bottom, color-stop($start_prc, $start), color-stop($end_prc, $end)); /* Chrome,Safari4+ */
    background: -webkit-linear-gradient(top, $start $start_prc, $end $end_prc); /* Chrome10+,Safari5.1+ */
    background: -o-linear-gradient(top, $start $start_prc, $end $end_prc); /* Opera 11.10+ */
    background: -ms-linear-gradient(top, $start $start_prc, $end $end_prc); /* IE10+ */
    background: linear-gradient(to bottom, $start $start_prc, $end $end_prc); /* W3C */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='$start', endColorstr='$end', GradientType=0 ); /* IE6-9 */
    -ms-filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='$start', endColorstr='$end', GradientType=0 ); /* IE6-9 */
}

@mixin gradient-vertical($startColor: #555, $endColor: #333) {
    background-color: mix($startColor, $endColor, 60%);
    background-image: -moz-linear-gradient(top, $startColor, $endColor); // FF 3.6+
    background-image: -webkit-gradient(linear, 0 0, 0 100%, from($startColor), to($endColor)); // Safari 4+, Chrome 2+
    background-image: -webkit-linear-gradient(top, $startColor, $endColor); // Safari 5.1+, Chrome 10+
    background-image: -o-linear-gradient(top, $startColor, $endColor); // Opera 11.10
    background-image: linear-gradient(to bottom, $startColor, $endColor); // Standard, IE10
    background-repeat: repeat-x;
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#{ie-hex-str($startColor)}', endColorstr='#{ie-hex-str($endColor)}', GradientType=0); // IE9 and down
}

@mixin textInput() {
    @include textControl();
    @include placeholder();
}
@mixin textarea() {
    @include textControl();
    @include placeholder();
    resize: vertical;
    min-height: 40px;
    height: 40px;
}
@mixin buttonGradient() {
    @include buttonReset();
    @include gradient();
    box-shadow: 0 0 3px rgba(0, 0, 0, 0.8);
    border: none;
    height: 30px;
    line-height: 30px;
    padding: 0 20px;
    color: #fff;
    text-transform: lowercase;
    font-family: $fancyFontFamily;
    font-weight: bold;
    font-size: 16px;
    text-decoration: none;
    display: inline-block;
    text-align: center;
    &:hover {
        @include gradientHover();
        color: #fff;
    }
}
@mixin buttonDelete() {
    @include buttonReset();
    border: none;
    height: 30px;
    line-height: 30px;
    padding: 0 20px;
    color: #f42d2d;
    text-transform: lowercase;
    font-family: $fancyFontFamily;
    font-size: 16px;
    text-decoration: none;
    display: inline-block;
    text-align: center;
    &:hover {
        color: #f42d2d;
    }
}
@mixin grayButtonGradient() {
    @include buttonReset();
    @include grayGradient();
    box-shadow: 0 0 2px #ebebeb;
    border-radius: 2px;
    border: 1px solid #c2c2c2;
    height: 23px;
    line-height: 23px;
    padding: 0 20px;
    color: #0155a7;
    text-transform: lowercase;
    font-family: $fancyFontFamily;
    font-size: round($baseFontSize * 1.167);
    font-weight: bold;
    display: inline-block;
    text-decoration: none;
    text-align: center;
    &.hover,
    &:hover {
        @include grayGradientHover();
        color: #0155a7;
    }
}

@mixin hr($color: #dddddd) {
    border: 0;
    height: 0;
    border-style: solid;
    border-width: 1px 0 0 0;
    border-color: $color;
}

@mixin leftSidebarMenu() {
    >ul{
        background: #fefefe;
        border: 1px solid #ddd;
        box-shadow: 0 0 10px rgba(0,0,0,0.1);
    }
    ul {
        list-style: none;
        margin: 0;
        padding: 15px 15px;
        ul {
            margin: 0 0 0 20px;
            padding: 0;
        }
        hr{
            @include hr();
        }
    }
}

@mixin row(){
    *zoom: 1;
    &:before, &:after{
        display: table;
        content: "";
        line-height: 0;
    }
    &:after{
        clear: both;
    }
}

@mixin fileUploadButton {
    .upload-file-button {
        @include grayButtonGradient();
    }
    .upload-file-text {
        border: none;
        background: none;
        padding-left: 10px;
        box-shadow: none;
        width: 300px;
    }
    .input-append {
        position: relative;
        .upload-file-hide,
        .logo {
            position: absolute;
            top: 0;
            left: 0;
        }
    }
}
