.show-more-btn {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  margin: 0 auto;
  width: 120px;
  height: 30px;
  border: 1px solid #339595;
  background: none;
  box-shadow: none;
  color: #339595;
  text-transform: uppercase;
  outline: none;
  border-radius: 5px;
  font-size: 11px;
  line-height: 11px;
  letter-spacing: 1px;
  font-weight: 700;
  overflow: hidden;
  text-decoration: none;
  cursor: pointer;
  white-space: nowrap;
  padding: 0 10px;
  text-align: center;

  &:hover {
    border: none;
    color: #fff;
    background: linear-gradient(180deg, #339595 0, #2c8282);
  }

  &_loading {
    color: #fff;

    &:before {
      background-color: #fff;
      z-index: 999;
    }
    &:after{
      content: "";
      display: block;
      position: absolute;
      animation: none;
      border: none;
      border-radius: 0;
      background-image: url(/images/page_preloader.gif);
      width: 25px;
      height: 15px;
      background-size: contain;
      background-repeat: no-repeat;
      z-index: 9999;
      margin: 0;
      left: 50%;
      top: 50%;
      transform: translate(-50%,-50%);
     }
  }
}