.popup-column {
  &__wrapper {
    display: flex;
  }

  &__wrapper_wide {
    width: 630px;
    flex-wrap: wrap;
    div {
      margin-bottom: 20px;
    }
  }

  &__wrapper_narrow {
    flex-wrap: wrap;
    max-width: 630px;
    min-width: 430px;
    div {
      margin-bottom: 20px;
    }
  }
  &__container {
    display: flex;
    flex-direction: column;
    min-width: 200px;
  }
  &__title {
    font-weight: 700;
    position: relative;
    padding-left: 21px;
    cursor: pointer;
    margin-bottom: 5px;
    &:before {
      content: '';
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      display: inline-block;
      width: 16px;
      height: 16px;
      background-image: url(/images/checkboxes.png);
      background-position: 0 0;
    }
    &_checked {
      &:before {
        background-position: -16px 0;
      }
    }
  }
  &__list {
    display: flex;
    flex-direction: column;
    background-color: #fff;
    margin: 0;
    list-style: none;
    padding-left: 0;
  }
  &__item {
    position: relative;
    margin: 5px 0;
    padding-left: 21px;
    cursor: pointer;
    &:before {
      content: '';
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      display: inline-block;
      width: 16px;
      height: 16px;
      background-image: url(/images/checkboxes.png);
      background-position: 0 0;
    }
    &_checked {
      &:before {
        background-position: -16px 0;
      }
    }
    &_disabled {
      color: gray;
    }
  }
}