.episodes-table {
  display: flex;
  position: relative;
  width: 100%;
  flex-wrap: wrap;
  background-color: white;
  border-radius: 0 0 10px 10px;
  box-shadow: 0 1px 0 0 #ccc;
  &__container {
    display: flex;
    position: relative;
    width: 100%;
  }
  &__summary {
    display: flex;
    position: relative;
    width: auto;
    padding: 25px 25px 25px 0;
    width: 40%;
  }
  &__summary-col {
    display: flex;
    position: relative;
    width: 50%;
    flex-direction: column;
    &:first-child {
      margin-right: 25px;
    }
  }
  &__reload {
    display: flex;
    position: relative;
    width: auto;
    align-items: center;
    text-decoration: none;
    cursor: pointer;
    &:hover {
      text-decoration: underline;
    }
  }
  &__reset {
    display: flex;
    position: relative;
    width: auto;
    align-items: center;
    margin-right: 25px;
    text-decoration: none;
    cursor: pointer;
    &:hover {
      text-decoration: underline;
    }
  }
  &__text {
    display: flex;
    position: relative;
    width: auto;
    align-items: center;
    padding-left: 5px;
    font-size: 11px;
    line-height: 11px;
    color: #000;
  }
  &__checkbox-list {
    display: flex;
    position: relative;
    width: 100%;
    flex-direction: column;
    list-style: none;
    margin: 0;
    padding: 0;
    max-height: 515px;
    overflow-y: auto;
  }
  &__checkbox-item {
    display: flex;
    position: relative;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    min-height: 25px;
    padding: 5px 10px 5px 21px;
    cursor: pointer;
    &:before {
      content: '';
      display: block;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: 0;
      width: 16px;
      height: 16px;
      background-image: url('/images/checkboxes2-football.png');
    }
    &_active {
      &:before {
        background-position: -16px 0;
      }
    }
    .text {
      opacity: .5;
    }
  }
  &-header {
    display: flex;
    width: 100%;
    height: 60px;
    justify-content: space-between;
    align-items: center;
    padding: 0 25px;
    line-height: 50px;
    background: white;
    position: relative;
    border-radius: 5px 5px 0 0;
    border-bottom: 1px solid rgba(0,0,0,.1);
    &-title {
      display: flex;
      width: 100%;
      justify-content: space-between;
      align-items: center;
      color: #1a1a1a;
      font-size: 14px;
      position: relative;
      z-index: 2;
    }
    &-title-text {
      display: flex;
      position: relative;
      align-items: center;
      font-size: 14px;
      font-weight: bolder;
    }
    &-title-play {
      display: flex;
      position: relative;
      width: auto;
      align-items: center;
      .icon {
        position: relative;
        display: block;
        width: 16px;
        height: 16px;
        background-image: url(/images/icons/svg/icon-play.svg);
        background-size: contain;
        background-repeat: no-repeat;
      }
    }
    &-settings-partners {
      display: flex;
      position: relative;
      align-items: center;
    }
    &-settings-partners-item {
      display: flex;
      position: relative;
      width: 81px;
      margin-left: 5px;
    }
    &-settings-partners-item-link {
      display: flex;
      position: relative;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 32px;
      cursor: pointer;
      color: #ccc;
      border: 1px solid #d6d2d0;
      border-radius: 4px;
      text-transform: uppercase;
      text-decoration: none;
      letter-spacing: 1px;
      font-size: 9px;
      line-height: 9px;
      &:hover {
        background-color: #ccc;
      }
    }
    &-buttons {
      display: flex;
      position: relative;
      width: auto;
      align-items: center;
      .player-details-football-map-header-settings {
        right: 0;
        transform: translateY(-60%);
      }
    }
    &-settings-button {
      display: flex;
      position: relative;
      justify-content: center;
      align-items: center;
      width: 32px;
      height: 32px;
      border: 1px solid #d6d2d0;
      border-radius: 4px;
      margin-left: 5px;
      cursor: pointer;
      &:hover {
        background-color: #ccc;
      }
      .icon-episodes-settings_gray {
        width: 13px;
        height: 13px;
        background-size: contain;
      }
    }
  }
  &-header-count {
    display: flex;
    position: relative;
    width: auto;
    padding: 0 10px;
    flex-direction: column;
    &__row {
      display: flex;
      position: relative;
      width: auto;
      align-items: center;
      text-transform: uppercase;
      font-size: 11px;
      line-height: 11px;
      white-space: nowrap;
      color: #1a1a1a;
      font-weight: 700;
      cursor: pointer;
      &:hover {
        .episodes-table-header-count__num {
          text-decoration: underline;
        }
        i {
          opacity: 1;
        }
      }
      i {
        opacity: .5;
      }
    }
    &__num {
      display: flex;
      position: relative;
      width: auto;
      font-size: 11px;
      line-height: 11px;
      font-weight: 700;
      padding: 0 5px;
      &_light {
        color: rgba(#1a1a1a,.5);
        padding: 0 5px;
      }
    }
  }
  &-content {
    display: flex;
    position: relative;
    width: 60%;
    flex-direction: column;
    padding: 25px;
  }
  &-column {
    display: flex;
    position: relative;
    width: 100%;
    flex-direction: column;
    &__search {
      display: flex;
      position: relative;
      width: 100%;
      padding-bottom: 5px;
    }
    &__input {
      display: flex;
      position: relative;
      width: 100%;
      height: 25px;
      align-items: center;
      padding: 0 30px 0 15px;
      border: none;
      background-color: #f2f2f2;
      font-size: 11px;
      line-height: 11px;
      border-radius: 3px;
    }
    &_group {
      margin-bottom: 20px;
      
      .episodes-table-column-title {
        display: flex;
        position: relative;
        width: 100%;
        height: 28px;
        justify-content: center;
        align-items: center;
        background: #B8E0DF;
        text-transform: uppercase;
        letter-spacing: 1px;
        border-style: solid;
        border-width: 1px;
        border-color: rgba(0, 0, 0, 0.03) white rgba(0, 0, 0, 0.06) rgba(255, 255, 255, 0.2);
        color: rgba(0, 0, 0, 0.5);
        font-size: 9px;
        line-height: 9px;
      }
      ul {
        display: flex;
        position: relative;
        width: 100%;
        max-height: 275px;
        flex-direction: column;
        flex-wrap: wrap;
        margin: 0;
        padding: 0;
        list-style: none;
      }
      .episodes-table-button {
        padding: 0;
        margin: 0;
        list-style: none;
        display: block;
        height: 35px;
        min-height: 35px;
        max-height: 40px;
        position: relative;
        border-right: 1px solid white;
        border-bottom: 1px solid white;
        cursor: pointer;
        flex-basis: calc(100% / 3);
        width: calc(100% / 3);
        background: #DBF0EF;
        &:hover {
          background: #B8E0DF;
        }
        &.active {
          background: #339595;
          .title {
            font-weight: bolder;
            color: white;
          }
          .text {
            opacity: 0.6;
            span {
              display: block;
              white-space: normal;
            }
          }
        }
        .title {
          position: absolute;
          left: 0;
          top: 0;
          bottom: 0;
          padding-left: 10px;
          padding-right: 35%;
          line-height: 12px;
          text-align: left;
          box-sizing: border-box;
          color: black;
          display: flex;
          align-items: center;
          width: 100%;
        }
        .text {
          opacity: 0;
          position: absolute;
          left: 65%;
          right: 0;
          top: 50%;
          display: block;
          transform: translateY(-50%);
          text-align: left;
          color: white;
          font-size: 9px;
          line-height: 11px;
        }
      }
      &:nth-child(2n + 1) {
        .episodes-table-column-title {
          background: #fcceb4;
        }
        .episodes-table-button {
          background: #feeee5;
  
          &:hover {
            background: #fcceb4;
          }
  
          &.active {
            background: #b74b4b;
            .title {
              font-weight: bolder;
              color: white;
            }
          }
        }
      }
      &:last-child {
        margin-bottom: 0;
      }
    }
    &__title {
      display: flex;
      position: relative;
      width: 100%;
      flex-direction: column;
      padding: 5px 0;
    }
    &__heading {
      display: flex;
      position: relative;
      width: auto;
      align-items: center;
      font-size: 13px;
      line-height: 13px;
      font-weight: 700;
      margin: 0;
      padding: 0 0 5px 0;
      color: #1a1a1a;
    }
    &__subheading {
      display: flex;
      position: relative;
      width: auto;
      height: 20px;
      font-size: 11px;
      line-height: 11px;
      color: #1a1a1a;
      margin: 0;
      padding: 0;
    }
  }
  &-button-popup {
    display: block;
    width: 100%;
    position: absolute;
    top: 100%;
    margin-top: 10px;
    background: rgba(37, 37, 37, 0.95);
    border-radius: 5px;
    z-index: 10;
    padding: 5px 10px 10px;
    &:before {
      content: "";
      display: block;
      position: absolute;
      left: 50%;
      top: 0;
      border-width: 10px 7px;
      border-style: solid;
      border-color: transparent transparent rgba(37, 37, 37, 0.95);
      margin: -20px 0 0 -7px;
    }
    ul {
      list-style: none;
      margin: 0;
      padding: 0;
      max-height: none;
      li {
        list-style: none;
        margin: 8px 0 0;
        padding: 0 0 0 22px;
        cursor: pointer;
        line-height: 14px;
        position: relative;
        display: block;
        width: 100%;
        color: white;
        &:before {
          content: '';
          background: transparent url('/images/checkbox_black2.png') 0 0 no-repeat;
          display: block;
          height: 16px;
          width: 16px;
          position: absolute;
          left: 0;
          top: -1px;
        }
        &.active {
          color: #ffc821;
          &:before {
            background-position: -18px 0;
          }
        }
      }
    }
    &.two-cols {
      width: 200%;
      transform: translateX(-25%);
      ul {
        width: 49%;
        display: inline-block;
        vertical-align: top;
      }
    }
  }
} 