//@import '../../../lib/containers/app/mixins';

.header {
  display: flex;
  background: white;
  height: 60px;
  padding: 0 25px 0 33px;
  border-bottom: 1px solid rgba(#1a1a1a, .1);

  &__link {
    position: absolute;
    right: 0;
    transform: translateX(100%);
    padding-left: 15px;
    font-size: 12px;
    line-height: 12px;
    color: rgba(26, 26, 26, 0.5);
    text-decoration: none;
    white-space: nowrap;

    &:hover {
      color: rgba(26, 26, 26, 0.7);
      text-decoration: none;
    }
  }

  .header-table {
    display: flex;
    width: 100%;
    align-items: center;
  }

  .header-row{
    display: flex;
    position: relative;
    width: 100%;
    justify-content: space-between;
    align-items: center;
  }

  .logo {
    display: block;
    width: 90px;
    height: 20px;
    background-size: contain;
  }

  .live-search {
    position: relative;
    width: 600px;
  }

  .link-iconed-wrapper {
    margin-right: 25px;
    position: relative;

    .link-iconed-text, .link-iconed-text span {
      max-width: 80px;
      color: #1a1a1a;
    }
  }

  .header-menu-item span,
  .link-iconed-text {
    overflow: hidden;
    color: rgba(white, .7);
  }

  .header-profile-link {
    position: relative;
    cursor: pointer;

    span, i {
      margin: 0;
      padding: 0;
      vertical-align: middle;
      line-height: 14px;
      height: 14px;
    }
    span {
      margin-left: 5px;
    }

    .icon-person {
      width: 18px;
      height: 18px;
      margin: 0;
      background-image: url('/images/icons/svg/icon-profile.svg');
      background-repeat: no-repeat;
      background-size: contain;
    }

    .upload-video__icon {
      width: 18px;
      height: 18px;
      display: inline-block;
      background-image: url('/images/icons/svg/icon-video-upload.svg');
      background-repeat: no-repeat;
      background-size: contain;
    }
  }
}

.header-row {
  display: flex;
  position: relative;
  width: 100%;
  justify-content: space-between;
  align-items: center;
}

.header-search-input {
  width: 100%;
  height: 35px;
  border-radius: 5px;
  color: #1a1a1a;
  float: right;
  font-size: 14px;
  font-weight: bold;
  padding: 5px 25px;
  border: none;
  background-color: rgba(#ededed, .5);

  &:focus {
    border: 1px solid #e1e1e1;
    border-top: 1px solid #cacaca;
    background-color: #EDEDED;
  }

  &::placeholder {
    font-weight: normal;
  }

  &:focus + .header-search-button {
    transform: translate(1450%, -50%);
    transition: .5s;
  }
}

.header-search-button {
  border: none;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  padding: 0;
  background: none;
  pointer-events: none;
  transition: .5s;

  i {
    display: block;
    width: 18px;
    height: 18px;
    border-radius: 0;
    border: none;
    background-image: url('/images/icons/svg/icon-search-lens.svg');
    background-repeat: no-repeat;
    background-size: contain;
    opacity: .35;

    &:after {
      display: none;
    }

    &:before {
      display: none;
    }
  }
}

.header-right-link {
  display: inline-block;
  float: right;
  margin-top: 5px;
  padding: 10px;
}

.header-column-right {
  display: flex;
  align-items: center;
}

.header-column-responsive {
  display: flex;
  position: absolute;
  width: auto;
  left: 50%;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  transform: translateX(-50%);
}

.list-unstyled {
  display: flex;
  position: relative;
  width: 100%;
  flex-direction: column;
  margin: 0;
  padding: 0;
  list-style: none;
}

.lang-wrapper {
  display: flex;
  position: relative;
  align-items: center;
  z-index: 100;
  width: 48px;

  .countries {
    display: flex;
    position: relative;
    background: transparent;
    border: none;
    border-radius: 3px;

    &.open {

      ul {
        margin: 0;
        display: block;
      }
    }

    .countries-label {
      cursor: pointer;
      position: relative;
      z-index: 5;

      .icon-language {
        display: block;
        width: 18px;
        height: 18px;
        background-image: url('/images/icons/svg/icon-language.svg');
        background-repeat: no-repeat;
        background-size: contain;
      }
    }

    ul {
      display: none;
      padding: 10px 0;
      margin: 0;
      width: 190px;
      position:absolute;
      background: white;
      box-shadow: 0 2px 5px rgba(0,0,0,0.5);
      right:0;
      top: 25px;
      &:after{
        content: "";
        display: block;
        width: 100%;
        clear: both;
        height: 0;
      }
    }
    li {
      text-align: center;
      margin: 6px 0 0 0;
      display: block;
      width: 25%;
      float: left;
    }
    a {
      background-repeat: no-repeat;
      display: inline-block;
      width: 100%;
      height: 25px;
      .flag-icon{
        outline-width: 1px;
        outline-style: solid;
        outline-color: #eee;
        transition: all 0.2s ease;
      }
      &:hover{
        .flag-icon{
          transform: scale(1.25);
        }
      }
    }
    span {
      background-repeat: no-repeat;
      display: inline-block;
      width: 100%;
      height: 20px;
    }
    $langs: ad, ae, af, ag, ai, al, am, ao, aq, ar, as, at, au, aw, ax, az, ba, bb, bd, be, bf, bg, bh, bi, bj, bl, bm, bn, bo, bq, br, bs, bt, bv, bw, by, bz, ca, cc, cd, cf, cg, ch, ci, ck, cl, cm, cn, co, cr, cs, cu, cv, cw, cx, cy, cz, de, dj, dk, dm, do, dz, ec, ee, eg, eh, en, er, es, et, fi, fj, fk, fm, fo, fr, ga, gb, gd, ge, gf, gg, gh, gi, gl, gm, gn, gp, gq, gr, gs, gt, gu, gw, gy, hk, hm, hn, hr, ht, hu, id, ie, il, im, in, io, iq, ir, is, it, je, jm, jo, ja, ke, kg, kh, ki, km, kn, kp, kr, kw, ky, kz, la, lb, lc, li, lk, lr, ls, lt, lu, lv, ly, ma, mc, md, me, mf, mg, mh, mk, ml, mm, mn, mo, mp, mq, mr, ms, mt, mu, mv, mw, mx, my, mz, na, nc, ne, nf, ng, ni, nl, no, np, nr, nu, nz, om, pa, pe, pf, pg, ph, pk, pl, pm, pn, pr, ps, pt, pw, py, qa, re, ro, rs, ru, rw, sa, sb, sc, sd, se, sg, sh, si, sj, sk, sl, sm, sn, so, sr, ss, st, sv, sx, sy, sz, tc, td, tf, tg, th, tj, tk, tl, tm, tn, to, tr, tt, tv, tw, tz, ua, ug, um, us, uy, uz, va, vc, ve, vg, vi, vn, vu, wf, ws, ye, yt, za, zm, zw;
    @each $lang in $langs {
      .#{$lang} {
        background: url("/images/languages/#{$lang}.svg") no-repeat;
        background-size: 26px;
      }
    }
  }
}


.demo-wrapper{
  display: none;
}

.lang-wrapper .countries ul,
.js-dropdown-profile{
  display: block !important;
}

.header .header-column-right.demo-column{
  width: 730px;

  .demo-wrapper{
    display: block;
    width: 120px;
    float: right;
    text-align: left;
    padding-left: 20px;
    padding: 10px 0 0 15px;
    .demo-title{
      color: #555;
      white-space: nowrap;
    }
    .demo-content{
      color: #000;
      font: bold 1em "PT Sans";
      white-space: nowrap;
    }
    .demo-help{
      background-size: 12px !important;
      width: 12px;
      height: 12px;
      opacity: 0.5;
      margin-left: 3px;
    }
  }
}


@media screen and (min-width: 1100px) {

  body.body-home {
    .header-column-smart-search-button{
      width: 108px;
      display: table-cell;

      .header-smartsearch-wrapper {
        display: table-cell;
        padding-top: 10px;
        padding-right: 20px;
        vertical-align: top;

        .ui-button {
          height: 29px;
          line-height: 11px;
          font-size: 0.9em;
          padding: 0 12px;
          white-space: nowrap;
        }
      }
    }
  }
}


@media screen and (max-width: 1420px) {
  .header {
    .link-iconed-primary {
      padding: 0px 10px;
      margin: 0;

      .link-iconed-text {
        display: none
      }
    }

    .header-menu-item.streams > span {
      display: none;
    }
  }
}

.home {

  &__text {
    display: flex;
    position: relative;
    width: 100%;
    justify-content: center;
    margin: 25px 0;
    font-size: 16px;
    line-height: 16px;
    color: #1a1a1a;
  }

  .header {
    display: flex;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: auto;
    height: auto;
  }

  .header-row {
    flex-direction: column;
    justify-content: center;
    height: 100%;
  }

  .header-column-responsive {
    position: relative;
    top: auto;
    left: auto;
    right: auto;
    bottom:auto;
    width: auto;
    // margin-bottom: 200px;
    margin-bottom: 50px;
    transform: none;
  }

  .live-search-results-panel {
    max-height: 40vh;
  }

  .header-column-right {
    position: absolute;
    top: 25px;
    right: 25px;

    .link-iconed-wrapper {
      margin-right: 25px;

      .dropdown-menu-container {
        top: 30px;
        right: 3px;
      }
    }

    .header-my-video-link .icon-video-player-play:after {
      border-left-color: #ff662b;
    }

    .link-iconed.link-iconed-primary .link-iconed-text {
      max-width: none;

      span {
        color: #1a1a1a;
      }
    }

    .lang-wrapper .countries {
      background: transparent;
    }
  }
}