.main-page {
  position: relative;

  &__details {
    display: flex;
    position: relative;
    width: 100%;
    padding: 25px;
    box-shadow: inset 0 5px 6px 1px rgba(0, 0, 0, 0.1);
  }

  &__left {
    display: flex;
    position: relative;
    min-width: 560px;
    width: 560px;
    flex-direction: column;
  }

  &__responsive {
    display: flex;
    position: relative;
    flex-grow: 1;
    flex-direction: column;
    padding: 0 13px 0 25px;
    width: calc(100% - 535px);
  }

  &__content {
    position: relative;
    width: 100%;
    min-height: 100px;
    background-color: #fff;
    border-bottom: 1px solid #c6c6c6;
    border-radius: 10px;
  }

  &__widgets {
    justify-content: space-between;
    align-items: flex-start;
    margin-top: 25px;
    display: flex;

    & .t-compare,
      .p-compare {
        display: flex;
        position: relative;
        width: 252px;
        min-height: 300px;
        flex-direction: column;
        background-color: white;
        border-radius: 10px;
        border-bottom: 1px solid #c6c6c6;
        ::-webkit-scrollbar {
          width: 6px;
        }
    
        ::-webkit-scrollbar-thumb {
          background: #ccc;
          border-radius: 4px;
        }
    
        ::-webkit-scrollbar-track {
          background: #fff;
        }

        &__arrow {
          display: flex;
          position: relative;
          width: auto;
          white-space: nowrap;
          font-size: 9px;
          line-height: 8px;
        }

        &__players-list {
          display: flex;
          position: absolute;
          min-width: 150%;
          top: 200%;
          left: -5px;
          flex-direction: column;
          margin: 0;
          padding: 0;
          list-style: none;
          font-size: 11px;
          line-height: 11px;
          background-color: white;
          box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
          z-index: 19;
          overflow: hidden;
          padding: 3px 0 2px 0;

          li {
            display: flex;
            position: relative;
            width: 100%;
            height: 25px;
            align-items: center;
            padding: 0 5px;
            white-space: nowrap;

            span {
              width: 13px;
              justify-content: flex-end;
              margin-right: 5px;
            }
          }
        }    
        &__header {
          display: flex;
          position: relative;
          width: 100%;
          height: 60px;
          justify-content: center;
          align-items: center;
          padding: 0 20px;
        }

        &__title {
          display: inline-block;
          position: relative;
          width: auto;
          white-space: nowrap;
          font-size: 11px;
          line-height: 11px;
          font-weight: 700;
          letter-spacing: 1px;
          text-transform: uppercase;
          cursor: pointer;
          color: grey;
          padding: 0 5px;
        }

        &__gear-options {
          position: absolute;
          top: 50%;
          margin-top: -8px;
          right: 20px;
          cursor: pointer;
          opacity: .5;

          &:hover {
            opacity: 1;
          }
        }
        
        &__teams {
          display: flex;
          position: relative;
          width: 100%;
          height: 30px;
          align-items: center;
          background-color: #dbf0ef;
          justify-content: space-between;      
        }

        &__team {
          width: 50%;
          display: flex;
          &-name {
            font-weight: 700;
            margin: 0 auto;
            text-align: center;
            max-width: 80%;
            line-height: 12px;
          }
        }
        &__player {
          display: flex;
          position: relative;
          width: 50%;
          align-items: center;
          white-space: nowrap;
          font-size: 11px;
          line-height: 11px;
          color: #1a1a1a;
          font-weight: 700;
          cursor: pointer;

          &-num {
            display: flex;
            position: relative;
            width: 13px;
            white-space: nowrap;
            color: rgba(26, 26, 26, 0.7);
            margin-right: 5px;
            font-weight: normal;
            justify-content: flex-end;
          }

          &-name {
            display: block;
            position: relative;
            width: auto;
            white-space: nowrap;
            margin-right: 5px;
            overflow: hidden;
            text-overflow: ellipsis;
          }

          //         
          & .btn-group {
            padding: 0;

            & .custom-select__dropdown {
              color: #1a1a1a;
              font-weight: 700;
              div {
                display: flex;
                max-width: 90px;
              }
            }
            
            & .dropdown-menu {
              bottom: auto;
              top: 100%;
              width: auto;
              background: #fff;

              li {
                a {
                  &:hover {
                    background: #dbf0ef;
                  }
                  div {
                    display: flex;
                    font-weight: 700;
                  }
                }
              }
            }
          }
        }

        &__control-row {
          display: flex;
          position: relative;
          width: 100%;
          height: 90px;
          align-items: center;
          padding: 0 20px;
        }

        &__logo {
          display: flex;
          position: relative;
          width: 100%;
          height: auto;
        }
        &__stats-container {
          max-height: 420px;
          overflow: auto;
          
        }

        &__logo-container {
          display: flex;
          position: relative;
          width: 25%;
          justify-content: center;
          align-items: center;

          & .t-compare__logo {
            margin-top: 20%;
          }
          & .t-compare__logo {
            &_player {
              position: absolute;
              bottom: 5px;
              left: -10px;
              width: 60%;
              opacity: .7;
              height: auto;
              align-self: flex-end;
  
              &-right {
                left: auto;
                right: -10px;
              }
            }
            &_team {
              position: absolute;
              bottom: 5px;
              width: 60%;
              opacity: .7;
              height: auto;
              align-self: flex-end;
  
              &-right {
                left: auto;
              }
            }
          }
          
          &_player {
            width: 35%;
          }
          &_teams {
            width: 35%;
            height: 55px;
          }
        }

        &__controls {
          display: flex;
          position: relative;
          width: 50%;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          margin-top: 5px;

          &_player {
            width: 30%;
          }
        }

        &__toggler {
          display: flex;
          position: relative;
          width: auto;
          white-space: nowrap;
          font-family: "PT Sans";
          font-size: 11px;
          line-height: 11px;
          letter-spacing: 1px;
          color: #1a1a1a;
          font-weight: 700;
          text-transform: uppercase;
          margin: 3px 0;
          border: none;
          background-color: transparent;
          outline: none;
          cursor: pointer;
        }

        &__stats {
          &-row {
            display: flex;
            position: relative;
            width: 100%;
            align-items: center;
            border-top: 1px solid #dbe1e2;
            font-size: 11px;
            line-height: 10px;
            color: #1a1a1a;
            padding: 0 20px;
            height: 35px;
          }
          &-num {
            display: flex;
            position: relative;
            width: 25%;
            justify-content: center;
            align-items: center;
            font-weight: 700;
            min-height: 30px;
          }
          &-name {
            display: flex;
            position: relative;
            width: 50%;
            justify-content: center;
            align-items: center;
            text-align: center;
          }
        }

        &__clickable-cell {
          background-color: rgba(71, 101, 104, 0.04);
          position: relative;
          height: 100%;
          cursor: pointer;
          &:hover {
            background-color: #dbf0ef;
          }
        }
      }
  
  }

  &__content-header {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 60px;
    line-height: 60px;
    padding: 0 25px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.3);
    
    .video__btn-container {
      position: relative;
      min-width: 10%;
    }

    .players-details-header-episodes {
      display: flex;
      align-items: center;
      z-index: 100;
      font-size: 11px;
      line-height: 11px;
      letter-spacing: 1px;
      cursor: pointer;
  
      i {
        opacity: .5;
        margin-right: 10px;
      }
  
      &:hover {
        text-decoration: underline;
  
        i {
          opacity: 1;
        }
      }
    }
  
    .episodes-table-header-count__num {
      font-weight: 700;
      margin-top: 2px;
  
      &_light {
        color: rgba(0, 0, 0, 0.5);
        padding: 0 0 0 5px;
      }
    }
  }

  &__side {
    display: flex;
  }

  &__map-wrapper {
    width: 380px;
    padding: 10px;
    position: relative;
    .selection_field {
      position: absolute;
      left: -24px;
      top: -30px;
      width: 360px;
      height: 700px;
    }
  }

  &__filters-wrapper {
    width: calc(100% - 375px);
    padding: 10px 5px 0px 20px;
  }

  &__map-field {
    position: relative;
    font-size: 0;
    min-height: 296px;
    
    img {
      width: 100%;
      height: auto;
    }
  }

  &__map-shots {
    position: absolute;
    top: 9px;
    left: 24px;
    font-size: 12px;
    width: 87%;
    height: 97%;
  }

  &__map-zones {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }

  &__map-footer {
    position: relative;
    display: flex;
    justify-content: space-between;
    width: 100%;
    height: 35px;
    background-color: #dbf0ef;
    margin-top: 1px;
  }

  &__map-footer-left {
    display: flex;
    align-items: center;
    
    //        
    .custom-select,
    .custom-checkbox {
      margin-right: 15px;
      &_mapfield-footer {
        .btn-group {
          padding-left: 0;
          .dropdown-menu {
            bottom: auto;
            top: 100%;
          }
        }
      }
    }

    //         
    .main-page__serve-btns {
      .btn-group {
        padding-left: 0;
        .dropdown-menu {
          bottom: auto;
          top: 100%;
          width: 220px;
        }
      }
    }
  }

  &__serve-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 35px;
    height: 35px;
    background: none;
    border: none;
    cursor: pointer;
    outline: none;

    &.active {
      background-color: #339595;
    }

    &::before {
      content: '';
      display: block;
      width: 15px;
      height: 15px;
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
    }

    &_in {
      margin-right: 1px;

      &::before {
        background-image: url(/images/icons/svg/icon-serve-in.svg);
      }

      &.active {
        &::before {
          background-image: url(/images/icons/svg/icon-serve-in_active.svg);
        }
      }
    }

    &_to {
      &::before {
        background-image: url(/images/icons/svg/icon-serve-to.svg);
      }

      &.active {
        &::before {
          background-image: url(/images/icons/svg/icon-serve-to_active.svg);
        }
      }
    }
  }

  &__map-footer-actions {
    display: flex;
    align-items: center;
    padding-right: 5px;

    a {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 25px;
      height: 25px;
      border-radius: 3px;
      cursor: pointer;
      margin-right: 5px;

      &:hover {
        background-color: rgba(#339595, 0.3);
      }
    }
  }
  
  &__range {
    &-popup-settings {
      position: absolute;
      width: auto;
      right: 15px;
      top: 100%;
      margin-top: 5px;
      padding: 20px;
      background: rgba(37, 37, 37, 0.95);
      color: white;
      border-radius: 5px;
      z-index: 101;
  
      &:before {
      content: "";
      display: block;
      width: 0;
      height: 0;
      border-width: 6px;
      border-style: solid;
      border-color: transparent transparent rgba(37, 37, 37, 0.95);
      position: absolute;
      top: -12px;
      right: 11px;
      }

      &.hidden {
        display: none;
      }
    }

    &-fieldset {
      padding: 2px 0;
      white-space: nowrap;
    }

    &-checkbox {
      box-sizing: border-box;
      color: white;
      display: block;
      cursor: pointer;
      position: relative;
      text-align: left;
      text-decoration: none;
      perspective-origin: 104.984px 9px;
      transform-origin: 104.984px 9px;
      font: normal 12px 'PT Sans', Tahoma, sans-serif;
      border: 0px none white;
      list-style: none outside none;
      outline: white none 0px;
      padding: 0px 0px 0px 22px;
      margin-bottom: 10px;
      height: 19px;
      width: auto;
      &:last-child {
        margin-bottom: 0;
      }

      &:before {
        box-sizing: border-box;
        color: white;
        content: '' '';
        display: block;
        height: 16px;
        left: 0px;
        position: absolute;
        text-align: left;
        top: 1px;
        width: 16px;
        perspective-origin: 8px 8px;
        transform-origin: 8px 8px;
        background: transparent url("http://beta.instatscout.com/assets/checkbox_black-48700f56efc9c4d4d54c0a041db856d2.png") no-repeat;
        border: 0px none white;
        font: normal 12px / 18px 'PT Sans', Tahoma, sans-serif;
        list-style: none outside none;
        outline: white none 0px;
      }
      &.active {
        color: #ffc821;
        border: 0px none #ffc821;
        outline: #ffc821 none 0px;

        &:before{
          box-sizing: border-box;
          color: #ffc821;
          content: '' '';
          display: block;
          height: 16px;
          left: 0px;
          position: absolute;
          text-align: left;
          top: 1px;
          width: 16px;
          perspective-origin: 8px 8px;
          transform-origin: 8px 8px;
          background: transparent url("http://beta.instatscout.com/assets/checkbox_black-48700f56efc9c4d4d54c0a041db856d2.png") right center no-repeat;
          border: 0px none #ffc821;
          font: normal 12px / 18px 'PT Sans', Tahoma, sans-serif;
          list-style: none outside none;
          outline: #ffc821 none 0px;
        }
      }
    }
    &-short-input {
      border: 1px solid rgba(255, 255, 255, 0.2);
      background: linear-gradient(to bottom, rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.3));
      color: white;
      border-radius: 3px;
      padding: 3px;
      margin: 0 10px 0 7px;
      text-align: center;
      width: 30px;
      font-size: 12px;

      &::-webkit-inner-spin-button,
      &::-webkit-outer-spin-button {
          -webkit-appearance: none; margin: 0px;
        }
        
      &[type='number'] {
        -moz-appearance:textfield;
      }
    }
  }




  &__map-zones-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    background: none;
    border: none;
    padding: 0;
    width: 25px;
    height: 25px;
    border-radius: 3px;
    text-align: center;
    cursor: pointer;
    outline: none;

    &::before {
      content: '';
      display: inline-block;
      width: 16px;
      height: 13px;
      background-image: url(/images/icons/icon-episodes-dashed.png);
      background-repeat: no-repeat;
      background-size: 100%;
      background-position: center;
    }

    &:hover,
    &.active {
      background-color: rgba(#339595, 0.3);
    }

    &:active {
      &::before {
        margin-top: 2px;
      }
    }
  }
}
