@mixin laptop {
  @media screen and (max-width: 1440px) {
    @content;
  }
}

@mixin desktop {
  @media screen and (max-width: 1800px) {
    @content;
  }
}

@mixin mobile {
  @media screen and (max-width: 576px) {
    @content;
  }
}

.stats-table {
  position: relative;
  width: 100%;
  padding-top: 15px;
  
  ::-webkit-scrollbar{
    width:  10px;
    height: 10px;
  }

  ::-webkit-scrollbar-thumb {
    background: #ccc;
    border-radius: 4px;
  }

  ::-webkit-scrollbar-track {
    background: #f2f2f2;
  }

  &:last-child {
    padding-bottom: 15px;

  ::-webkit-scrollbar{
    width:  10px;
    height: 10px;
  }

  ::-webkit-scrollbar-thumb {
    background: #ccc;
    border-radius: 4px;
  }

  ::-webkit-scrollbar-track {
    background: #f2f2f2;
  }
  }

  &__wrapper {
    overflow: auto;
    max-height: 600px;
  }
  &__header {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 40px;
    padding: 0 10px;
  }

  &__proceed {
    text-align: center;
    cursor: pointer;
    height: 30px;
    &:hover {
      font-weight: bold;
    }
  }

  &-checkbox-opp {
    display: flex;
  }
  
  .overall {
    background-color: #f8f8f8;
    height: 30px;
    td {
      text-align: center;
    }
  }

  .player-number {
    width: 30px;
  }
  .player-name {
    font-weight: bold;
  }

  &__header-left {
    display: flex;
    align-items: center;
  }

  &__name {
    position: absolute;
    display: flex;
    text-align: center;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    color: #1a1a1a;
    font-size: 11px;
    white-space: nowrap;
    text-transform: uppercase;

    input{
      display: flex;
      position: relative;
      width: auto;
      max-width: 700px;
      text-align: center;
      background: none;
      color: #1a1a1a;
      font-size: 11px;
      line-height: 11px;
      font-weight: normal;
      text-transform: uppercase;
      border: 1px solid rgba(193,193,193,0.4);
    }
    
    &:after{
      content: '';
      display: inline-block;
      margin-left: 5px;
      position: relative;
      width: 12px;
      height: 12px;
      background-image: url('/images/icons/svg/icon-edit_volleyball.svg');
      background-size: contain;
      background-repeat: no-repeat;
      opacity: 0;
    }
    &:hover:after{
      opacity: .7;
    }
  }

  &__actions {
    display: flex;
    align-items: center;
  }

  &__action-link {
    position: relative;
    display: flex;
    align-items: center;
    margin-right: 10px;
    color: rgba(0, 0, 0, 0.5);
    cursor: pointer;

    &:last-child {
      margin-right: 0;
    }

    & > span {
      margin-left: 2px;
      font-size: 10px;
      line-height: 10px;
    }

    i {
      opacity: 0.5;
    }

    &:hover {
      color: #1a1a1a;

      i {
        opacity: 1;
      }
    }
  }

  &__table {
    width: 100%;
    border-collapse: separate;
    border-spacing: 0;
    font-size: 11px;
  }

  &__table-header {
    height: 30px;
    background-color: #dbf0ef;
    .table-team-title {
      text-align: left;
      padding-left: 5px;
    }

    th {
      text-align: center;
      font-weight: 400;
    }
  }

  &__table-row {

    td {
      text-align: center;
      border-bottom: 1px solid #dbe1e2;
      td {
        border: none;
      }
    }
  }

  th {
    font-size: 11px;
    font-weight: normal;
    text-align: center;
    min-width: 45px;
    position: sticky;
    top: 0;
    background-color: #dbf0ef;
    z-index: 10;

    &:last-child {
      display: none;
    }

    &:before {
      content: '';
      display: block;
      position: absolute;
      left: 50%;
      top: 100%;
      border: 5px solid transparent;
      width: 0;
      height: 0;
      border-bottom-color: black;
      margin: -15px 0 0 -5px;
      opacity: 0;
      pointer-events: none;
    }
      .th-tooltip{
        position: absolute;
        left: 50%;
        top: 80%;
        z-index: 10;
        margin-top: -5px;
        width: 125px;
        background: black;
        padding: 5px 5px;
        text-align: center;
        transform: translateX(-50%);
        box-shadow: 0 3px 5px rgba(0,0,0,0.2);
        opacity: 0;
        transition: all 0.1s ease;
        pointer-events: none;
        color: white;
      }
  
      &:hover {
        background: darken(#dbf0ef, 10);
        cursor: pointer;
        z-index: 11;
        &:before {
          opacity: 1;
        }
        .th-tooltip {
          top: 100%;
          opacity: 1;
        }
      }
      &:nth-last-child(-n+3) .th-tooltip{
        transform: none;
        left: auto;
        right: 2px;
      }
      &:last-child .th-tooltip {
        right: 0 !important;
      }
      &:focus{
        outline: none;
      }
      &:first-child {
        text-align: right;
        padding: 0 5px;
        width: 250px;
        min-width: 250px;
        max-width: 250px;
        
        &:before {
          opacity: 0;
        }

        &:hover {
          background-color: #dbf0ef;
          cursor: default
        }

        .team-color{
          display: inline-block;
          width: 9px;
          height: 9px;
          border-radius: 50%;
      
          &.red{
            background-color: red;
          }
      
          &.white{
            background-color: white;
          }
        }
      }
    }
    .stats-cell {
      display: flex;
      width: 100%;
      cursor: pointer;

      .player-number {
        padding-left: 10px;
      }

      &:hover {
        text-decoration: underline;
        .match-play {

          i {
            opacity: 1;
          }
        }

        .match-date, 
        .match-team-opponent span, 
        .match-score {
          text-decoration: underline;
        }
      }

      &__home, 
      &__opp {
        display: flex;
        position: relative;
        width: 100%;
        align-items: center;
        justify-content: center;
      }

      &__home {
        padding-bottom: 5px;
      }

      &__opp {
        padding-top: 8px;
      }

      .match-play {
        margin: 5px;

        i {
          margin-right: 5px;
        }
      }

      .match-date {
        display: flex;
        position: relative;
        width: 50px;
        justify-content: flex-start;
        align-items: center;
      }

      .match-team-opponent {
        display: flex;
        position: relative;
        align-items: center;
        padding-right: 5px;
        flex-grow: 2;

        div:first-child {
          display: inline-flex;
          width: 17px;
          opacity: .5;
        }

        div.opponent {
          font-size: 9px;
        }

        .opponent-name {
          display: inline-flex;
          position: relative;

          span {
            display: inline-block;
            position: relative;
            width: 100%;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            text-align: left;
          }
        }
      }

      .match-score {
        display: flex;
        position: relative;
        justify-content: center;
        align-items: center;
        margin-left: auto;
        width: 45px;

        &_green {
          color: green;
          text-decoration: none;
        }

        &_red {
          color: red;
          text-decoration: none;
        }

        &.for-opp {
          width: 35px;
          flex-direction: column;
        }
      }

      &__player {
        display: flex;
        position: relative;
        width: 100%;
        align-items: center;

        &:hover {

          .match-table__player-number {
            text-decoration: underline;
          }

          .match-table__player-name {
            text-decoration: underline;
          }
        }

        .match-table__player-number {
          display: flex;
          width: 20px;
          padding: 0 10px 0 10px;
          color: rgba(0,0,0,.5);
          position: relative;
          align-items: center;
          box-shadow: none;
          border: none;
          text-align: right;
        }

        .match-table__player-name {
          text-align: left;
          width: 100px;
          font-weight: 700;
          display: flex;
          padding: 0 0 0 10px;
          position: relative;
          align-items: center;
          box-shadow: none;
          border: none;
          min-width: 120px;

          a {
            color: black;
            text-decoration: none;
          }
        }
      }
    }
    .clickable-cell, 
    .cell {
      position: relative;
      padding: 0;

      table {
        border-spacing: 0;
        width: 100%;
      }
      tr {
        display: flex;
        flex-direction: column;
        height: 30px;
        width: 100%;
        justify-content: space-around;
        
        .td-loader {
          width: 100%;
          max-height: 30px;
          position: absolute;
        }

        td {
          display: flex;
          flex-direction: column;
          justify-content: space-around;
          height: 100%;
          border: 0;
        }
      }

      &.sorted-by {
        font-weight: 700;
      }
    }

    .clickable-cell {
      background-color: rgba(71, 101, 104, 0.04);

      .td-loader {
        width: 100%;
        max-height: 30px;
        position: absolute;
      }
      tr {
        td:hover {
          background-color: rgba(71, 101, 104, 0.1);
          cursor: pointer;
        }
      }
    }

    .cell {
      background-color: transparent;
    }

    .percents {
      table {
        margin: 0 auto;
        border-spacing: 0;
        width: 100%;
      }
      tr {
        display: flex;
        flex-direction: column;
        height: 25px;
        width: 100%;
        justify-content: space-around;
        td {
          display: flex;
          flex-direction: column;
          justify-content: space-around;
          height: 100%;
          border: 0;
        }
      }
      &.sorted-by {
        font-weight: 700;
      }
    }

    &__action-link {
      margin-left: 5px;
    }

    &__opponents-toggler {
      display: flex;
      position: relative;
      align-items: center;
      color: #1a1a1a;
      cursor: pointer;
      text-transform: none;
      font-size: 12px;
      margin-right: 10px;
      &:before{
        content: '';
        display: inline-block;
        width: 16px;
        height: 16px;
        margin-right: 5px;
        float: left;
        background: url('/images/checkboxes.png');
      }
      &.checked:before{
        background-position: -16px 0;
      }
    }
}

.table-header {
  height: 30px;
  background-color: #dbf0ef;
  .table-team-title {
    text-align: left;
    padding-left: 5px;
  }

  th {
    text-align: center;
    font-weight: 400;
  }
}
.team-stats {
  &-tabs-download {
    display: flex;
    position: relative;
    width: auto;
    justify-content: center;
    align-items: center;
    margin-left: auto;
    margin-right: 5px;
  }
  &-header-download-link {
    display: flex;
    position: relative;
    align-items: center;
    font-size: 10px;
    line-height: 10px;
    margin-right: 10px;
    color: rgba(0, 0, 0, 0.5);
    cursor: pointer;
    padding-top: 1px;

    &:last-child {
      margin-right: 0;
    }

    &:before {
      content: '';
      position: relative;
      width: 14px;
      height: 14px;
      background-image: url(/images/icons/svg/icon-download.svg);
      background-size: contain;
      background-repeat: no-repeat;
      display: inline-block;
      margin-right: 5px;
      top: 0;
      opacity: 0.7;
    }
  }
  &-header-settings-button {
    display: inline-block;
    white-space: nowrap;
    cursor: pointer;
    position: relative;
    margin: 0 5px;
    &:after{
      content: '';
      display: block;
      width: 15px;
      height: 15px;
      background-repeat: no-repeat;
      background-position: center top;
      opacity: 0.7;
    }

    &_add:after{
      background-image: url('/images/icons/svg/icon-add.svg');
    }
    &_remove:after{  
      background-image: url('/images/icons/svg/icon-remove.svg');
    }
    &_up:after{  
      background-image: url('/images/icons/svg/icon-top.svg');
    }
    &_down:after{  
      background-image: url('/images/icons/svg/icon-bottom.svg');
    }
    &_reset:after{  
      background-image: url('/images/icons/svg/icon-reset-table.svg');
    }
    
  }
  &-header-opponents-toggler,
  &-show-avatar {
    display: flex;
    position: relative;
    align-items: center;
    color: #1a1a1a;
    cursor: pointer;
    text-transform: none;
    font-size: 12px;
    margin-right: 10px;
    &:before{
      content: '';
      display: inline-block;
      width: 16px;
      height: 16px;
      margin-right: 5px;
      float: left;
      background: url('/images/checkboxes.png');
    }
    &.checked:before{
      background-position: -16px 0;
    }
  }
}

.results__avatar {
  display: flex;
  width: 55px;
  height: 55px;
  align-items: flex-end;
  align-self: flex-end;
  
  &-pic {
    display: block;
    max-width: 100%;
    height: auto;
    max-height: 100%;
    margin: 0 auto;
  }
}

.popup-container {
  display: inline-block;
  width: 200px;
  padding: 10px 10px 5px;
  position: absolute;
  z-index: 1000;
  background: rgba(26, 26, 26, .8);
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, .3);
  overflow: visible;
  border-radius: 8px;
  transform: translate(13%, 35px);

  @include mobile {
    padding: 5px 5px 0;
    width: 200px;
    transform: translate(0%, -195px);
  }

  &:after {
    content: '';
    display: block;
    border-left: 8px solid transparent;
    border-right: 8px solid transparent;
    border-top: 8px solid rgba(26, 26, 26, .8);
    height: 0;
    width: 0;
    position: absolute;
    top: 100%;
    left: 50%;
    margin: 0 0 0 -8px;
  }

  &__loading {
    .ui-button{
      visibility: hidden;
    }
    &:before{
      content: '';
      display: block;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      width: 38px;
      height: 20px;
      background-image: url('/images/page_preloader.gif');
      background-repeat: no-repeat;
      background-size: contain;
    }
  }

  &.popup-matches-episodes {
    background: rgba(26, 26, 26, 0.8);

    &:after {
      content: '';
      display: block;
      border-left: 8px solid transparent;
      border-right: 8px solid transparent;
      border-top: 8px solid rgba(26, 26, 26, .8);
      height: 0;
      width: 0;
      position: absolute;
      top: 100%;
      left: 50%;
      margin: 0 0 0 -8px;
    }

    &__position-bottom {
      bottom: auto;

      &:after {
        top: auto;
        bottom: 100%;
        margin-top: -8px;
        border-bottom: 8px solid rgba(26, 26, 26, .8);
        border-top-width: 0;
      }
    }
  }

  .ui-button {
    display: block;
    width: 100%;
    margin-bottom: 5px;
    position: relative;
    height: 30px;
    padding: 0 5px 0 31px;
    box-shadow: 0 1px 3px -1px rgba(0, 0, 0, .8);

    @include mobile {
      height: 30px;
      font-size: 11px;
      line-height: 11px;
      padding: 0 0 0 31px;
    }

    &:nth-last-child(2) {

      .icon-play_volleyball_circle {
        background-image: url('/images/icons/svg/icon-play.svg');
        background-repeat: no-repeat;
        background-size: contain;
      }
    }

    &:last-child {

      .icon-play_volleyball_circle {
        background-image: url('/images/icons/icon-boxscore_volleyball.png');
        background-repeat: no-repeat;
        background-size: contain;
        margin-left: 3px;
      }
    }

    .icon-play_volleyball_circle {
      width: 16px;
      height: 16px;
      position: absolute;
      left: 6px;
      top: 50%;
      display: inline-block;
      transform: translateY(-50%);
      background-image: url('/images/icons/svg/icon-play.svg');
      background-size: contain;
      background-repeat: no-repeat;
    }
  }
}
