@mixin laptop {
  @media screen and (max-width: 1360px) {
    @content
  }
}

.player-card-dropdown {
  font-size: 12px;
  color: black;
  position: relative;

  .player-card-stats-dropdown-inner {
    width: 150%;
    position: absolute;
    left: 0;
    top: 100%;
    z-index: 1000;
    background: #f2f2f2;
    border-radius: 2px;
    border-bottom: 1px solid #c6c6c6;
    box-shadow: 0 0 10px rgba(0,0,0,0.2);
    margin-top: -5px;
    max-height: 60vh;
    overflow-y: scroll;

    @include laptop {
      margin-top: 0;
    }

    &.loading-active_global{
      &:after{
        content: '';
        background: #e9e9e9;
        height: 100%;
        left: 0;
        position: absolute;
        top: 0;
        width: 100%;
        z-index: 105;
      }
      &:before{
        animation: three-quarters 1250ms infinite linear;
        border: 8px solid #A7A7A7;
        border-right-color: transparent;
        border-radius: 16px;
        box-sizing: border-box;
        content: '';
        display: inline-block;
        height: 32px;
        left: 50%;
        margin: -16px 0 0 -16px;
        overflow: hidden;
        position: absolute;
        text-indent: -9999px;
        top: 50%;
        width: 32px;
        z-index: 110;
      }
    }

    .player-card-stats-dropdown-list-hr{
      margin: 10px 10px;
      border-top: 2px solid #ccc;
    }

    .player-card-stats-dropdown-list {
      list-style: none;
      margin: 0;
      padding: 0;
      text-align: left;
      li {
        display: flex;
        height: 26px;
        line-height: 26px;
        white-space: nowrap;
        padding: 0 0 0 10px;
        position: relative;
        cursor: pointer;
        width: 100%;
        .title{
          width: 70%;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
          display: block;
          text-align: left;
          .match-score_green{
            display: inline;
            color: green;
          }
          .match-score_red{
            display: inline;
            color: red;
          }
        }
        &:hover{
          background: #d6d6d6;
        }
      }

      &.player-card-stats-dropdown_dates{
        .date{
          position: absolute;
          right: 10px;
          top: 0;
          height: 26px;
          display: block;
          widows: 70px;
          font-size: 10px;
          line-height: 26px;
          color: #8ea3a6;
        }
      }

      &.player-card-stats-dropdown_checkboxes{
        li{
          .title {
            &:before{
              content: "";
              display: inline-block;
              float: left;
              width: 16px;
              height: 16px;
              margin: 5px 5px 5px 0;
              background: url('/images/checkboxes.png');
              background-position: 0 0;
            }
            &__score {
              margin: 0 10px 0 5px;
              color: black;
            }

            &__score_lose {
              color: red;
            }
            
            &__score_win {
              color: green
            }
          }

          &.active{
            .title:before{
              background-position: -16px 0;
            }
          }
        }
      }

    }
    .loading-block{
      position: relative;
      height: 60px;
      line-height: 60px;
      text-align: center;
      padding-left: 45px;
      background: #e9e9e9;
      margin-top: 10px;
      display: none;
      .loading:after{
        left: 35%;
      }
      .loading:before{
        display: none;
      }
    }
    &.loading-active{
      .loading-block{
        display: block;
      }
    }
  }

}
