select.selectBox {
    display: none !important;
}

.selectBox-dropdown {
    background: #fff;
    border-radius: 3px;
    border: 1px solid #dbe1e2;
    color: #476568;
    cursor: pointer;
    display: inline-block;
    font-size: 10px;
    height: 30px;
    line-height: 1.5;
    margin-bottom: 10px;
    position: relative;
    text-align: left;
    text-decoration: none;
    outline: none;
    vertical-align: middle;
    width: 100% !important;

    .selectBox-arrow {
        border-left: 5px solid transparent;
        border-right: 5px solid transparent;
        border-top: 5px solid #476568;
        height: 0;
        position: absolute;
        top: 12px;
        right: 10px;
        width: 0;
    }

    .selectBox-label {
        display: inline-block;
        overflow: hidden;
        padding: 7px 25px 7px 8px;
        white-space: nowrap;
        width: 100% !important;
    }

    &.selectBox-menuShowing-bottom {
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
    }

    &.selectBox-menuShowing-top {
        border-top-left-radius: 0;
        border-top-right-radius: 0;
    }

    &.selectBox-disabled {
        .selectBox-arrow {
            border-color: #666;
            opacity: .5;
            filter: alpha(opacity=50);
        }
    }

    &.diagram-switcher {
        .selectBox-label {
            color: #000;
            font-size: 10px;
            padding: 8px 17px 2px 8px;
            position: relative;
            text-align: right;
        }
        .selectBox-arrow {
            right: -1px;
        }
    }

    &.playlist-type {
        border: 1px solid #fff;
        border-radius: 7px;
        box-shadow: 0 1px 3px -1px rgba(0,0,0,0.8);
        @include linearGradient(#f7f7f7, #e1e1e1);
        color: #000;
        font-size: 11px;
        text-transform: none;

        .selectBox-arrow {
            border-left: 4px solid transparent;
            border-right: 4px solid transparent;
            border-top: 7px solid #1a1a1a;
            height: 0;
            position: absolute;
            top: 12px;
            right: 10px;
            width: 0;
        }
    }

    &.compare-switcher {
        background: none;
        border: none;
        color: #fff;
        font-size: 13px;
        height: 19px;
        margin: 0;

        .selectBox-label {
            padding: 0;
        }
        .selectBox-arrow {
            border-top: 5px solid #fff;
            right: 0;
            top: 7px;
        }
    }
}

/* Dropdown menu */
.selectBox-dropdown-menu {
    position: absolute;
    z-index: 99999;
    min-width: 152px;
    max-height: 200px;
    min-height: 1em;
    border: solid 1px #dbe1e2;
    background: rgba(255,255,255,0.95);
    box-shadow: 0 0 4px 0px rgba(0,0,0,0.2);
    overflow: auto;
    -webkit-overflow-scrolling: touch;

