}

.selectBox-inline {
    background: #FFF;
    border-radius: 4px;
    border: solid 1px #dbe1e2;
    display: inline-block;
    min-width: 150px;
    outline: none;
    overflow: auto;

    &:focus {
        border-color: #666;
    }
    &.selectBox-disabled {
        color: #888 !important;

        .selectBox-options {
            a {
                background-color: transparent !important;
            }
        }
    }
}

.selectBox-options,
.selectBox-options li,
.selectBox-options li a {
    cursor: pointer;
    display: block;
    list-style: none;
    margin: 0;
    padding: 0;
}

.selectBox-options {

    &.selectBox-options-top{
        border-bottom: none;
        margin-top: 1px;
        border-radius: 2px;
    }
    &.selectBox-options-bottom{
        border-top: none;
        border-radius: 2px;
    }

    li {
        &.selectBox-hover {
            a {
                background-color: #476568;
                color: #fff;
            }
        }
        &.selectBox-disabled {
            a {
                background-color: transparent;
                color: #888;
            }
        }
        &.selectBox-selected {
            a {
                background-color: #efefef;
                color: #808080;
            }
        }

        a {
            background: 6px center no-repeat;
            color: #000;
            font-size: 10px;
            line-height: 2;
            overflow: hidden;
            padding: 0 25px 0 10px;;
            text-align: right;
            text-decoration: none;
            white-space: nowrap;
        }
    }

    .selectBox-optgroup {
        background: #EEE;
        color: #666;
        font-weight: bold;
        line-height: 1.5;
        padding: 0 .3em;
        white-space: nowrap;
    }

    &.compare-switcher-selectBox-dropdown-menu {
        margin: -20px 0 0 -1px;

        li {
            a {
                text-align: left;
            }
        }
    }
}

.selectBox {
    &.selectBox-disabled {
        color: #888 !important;
    }

    &.statistics-compare-select {
        margin: 10px 0 9px;
    }
}

.compare-switcher {
    .btn-group {
        width: 100%;

        &:active {
            box-shadow: none;
        }
    }
    .btn {
        background: none !important;
        border: none !important;
        color: #fff !important;
        font-size: 13px;
        margin: 0;
        padding: 0;
        text-align: left;
        width: 100%;
    }
    .caret {
        border-top: 5px solid #fff;
        position: absolute;
        right: 0;
        top: 7px;
    }
    .open {
        .btn-default {
            &.dropdown-toggle {
                background-color: transparent;
                border-color: transparent;
                box-shadow: none;
                color: #fff;
            }
        }
    }
    .dropdown-menu {
        border-radius: 0;
        font-size: 10px;
        margin-left: -10px;
        width: 220px;

        > li {
            > a {
                padding: 3px 10px;
            }
        }
    }
}


.select-connect {
    &.selectBox-dropdown {
        border: 1px solid #666666;
        border-radius: 5px;
        box-shadow: 0 1px 1px 0px rgba(0, 0, 0, 0.5);
        @include linearGradient(#616161, #515151);
        color: #fff;
        font-size: 10px;
        height: 19px;

        &:after {
            @include linearGradient(#545656, #464848);
            content: '';
            height: 17px;
            position: absolute;
            right: 20px;
            top: 0;
            width: 1px;
        }
        .selectBox-label {
            padding: 1px 25px 1px 9px;
        }
        .selectBox-arrow {
            border-left: 3px solid transparent;
            border-right: 3px solid transparent;
            border-top: 5px solid #b3b3b3;
            right: 7px;
            top: 6px;
        }
    }
}
.select-connect-selectBox-dropdown-menu {
    &.selectBox-options {
        background: #616161;
        margin: 0;

        &:before {
            display: none;
        }
        li {
            &.selectBox-selected {
                a {
                    background-color: #777;
                }
            }
            a {
                color: #ddd;
                line-height: 1.5;
                text-align: left;
            }
        }
    }
    &.selectBox-dropdown-menu {
        border: solid 1px #666666;
    }
}

/*Regular dropdown menu*/
.selectBox-dropdown-menu.ui-select-selectBox-dropdown-menu{
  border-radius: 6px;
  border: 1px solid #d4d4d4;
  background-image: linear-gradient(to bottom, #e1e1e1 0%, white 100%);
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
  margin: 0;
  li{
    a{
      font-size: 11px;
      padding-top: 4px;
      padding-bottom: 4px;
    }
	}
	&:before{
		display: none;
	}
}
/*Dark regular dropdown menu*/
.selectBox-dropdown-menu.ui-select-dark-selectBox-dropdown-menu{
	background-color: #4d4d4d;
  background-image: linear-gradient(180deg, rgba(73, 73, 73, 0.49) 0%, rgba(95, 95, 95, 0.52) 100%);
  border: none;
  box-shadow: 0 0 5px rgba(247, 238, 180, 0.4);
	li{
		a{
			color: #fff;
		}
		&.selectBox-selected a{
			background-color: #464646;
		}
		&.selectBox-disabled a{
			color: #d1d1d1;
		}
	}
	&:before{
		border-top: 5px solid white;
	}
}

.selectBox-dropdown-menu.video-builder-dropdown-menu {
  background: rgba(0,0,0, 0.3);
  border: none;
  box-shadow: 0 0 5px rgba(247, 238, 180, 0.4);
  li{
    a{
      color: #fff;
    }
    &.selectBox-selected a{
      background: rgba(0,0,0, 0.1);
    }
    &.selectBox-disabled a{
      color: #d1d1d1;
    }
  }
  &:before{
    border-top: 5px solid white;
  }
}
