@mixin marker_ending {
  content: "";
  display: block;
  position: absolute;
  background-color: red;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
}

.marker__wrapper {
  display: inline;
  &:hover {
    z-index: 100;
    cursor: pointer;
    .shot,
    .pointer {
      z-index: 200;
      background-color: #0066ff;
      &:after {
        border-left-color: #0066ff;
      }
      &.shot_miss {
        background-color: transparent;
        border-color: #0066ff;
        &::before,
        &::after {
          background-color: #0066ff;
        }
      }
      &.shot_out {
        background-color: transparent;
        &::before,
        &::after {
          background-color: #0066ff;
        }
      }
    }
  }
}

.shot {
  display: block;
  position: absolute;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: #000;
  cursor: pointer;
  text-decoration: none;
  box-shadow: 0 0 2px 1px rgba(#f5c690, 1);
  transform: translate(-50%, -50%);
  z-index: 100;

  &_miss {
    background-color: transparent;
    border: 1px solid #000;

    &::before {
      @include marker_ending;
      background-color: black;
      width: 7px;
      height: 1px;
    }

    &::after {
      @include marker_ending;
      background-color: black;
      width: 1px;
      height: 7px;
    }
  }

  &_out {
    background-color: transparent;
    box-shadow: none;
    width: 8px;
    height: 8px;

    &::before {
      @include marker_ending;
      width: 8px;
      height: 1.3px;
    }

    &::after {
      @include marker_ending;
      width: 1.3px;
      height: 8px;
    }
  }

  &_point {
    width: 13px;
    height: 13px;
    background-color: red;
    color: #fff;

    &:hover {
      color: #fff;
    }

    &::before {
      content: "P";
      position: absolute;
      left: 50%;
      top: 52%;
      transform: translate(-50%, -50%);
      font-size: 10px;
      line-height: 10px;
      font-weight: 700;
    }
  }

  &:hover {
    & > div {
      opacity: 1;
    }
  }

  &__tooltip {
    position: absolute;
    display: flex;
    flex-direction: column;
    top: -10px;
    left: 50%;
    transform: translate(-50%, -100%);
    font-size: 10px;
    line-height: 10px;
    color: #fff;
    padding: 15px;
    background-color: rgba(26,26,26,0.95);
    box-shadow: 0 0 10px rgba(245,247,216,0.3);
    border-radius: 3px;
    white-space: nowrap;
    pointer-events: none;
    opacity: 0;
    z-index: 2000;

    & > span {
      white-space: nowrap;
    }

    &::after{
      content: "";
      width: 0;
      height: 0;
      border-left: 4px solid transparent;
      border-right: 4px solid transparent;
      border-top: 8px solid rgba(26,26,26,0.95);
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      bottom: -8px;
      z-index: 2;
    }

    &_bottom {
      transform: translate(-50%, 100%);
      top: auto;
      bottom: -10px;
      &::after {
        border-top-width: 0;
        border-bottom: 8px solid rgba(26,26,26,0.95);
        bottom: auto;
        top: -8px;
        margin-left: -4px;
        transform: translateX(0);
      }
    }
  }

  &__result {
    color: #ffc821;
    font-weight: 700;
  }

  &__player-name {
    font-weight: 700;
    margin-bottom: 4px;
  }

  &__result,
  &__opponent-team,
  &__period-info {
    margin-bottom: 2px;
  }

}

.pointer {
  position: absolute;
  height: 1.3px;
  background-color: #999;
  transform-origin: left center;

  &.middle {
    &:first-child {
      &:after {
        display: none;
      }
    }

  }

  &.vector {
    &_miss {
      background-color: #ff0000,
    }
    &_point {
      background-color: #000,
    }
  }
}
