.header-stats-item-wrapper {
  position: relative;
}

.header-stats-item {
  width: auto;
  text-align: center;
  display: inline-block;
  vertical-align: middle;
  color: black;
  padding: 0 10px;
  border-bottom: 1px solid transparent;

  &__loader {
    z-index: 2;
    position: absolute;
    top: 50%;
    left: 50%;

    &:before {
      background: rgba(255, 255, 255, 0.85);
      content: '';
      left: 0;
      position: absolute;
      top: -25px;
      width: 100%;
      z-index: 100;
    }

    &:after{
      animation: three-quarters 1250ms infinite linear;
      border: 8px solid #A7A7A7;
      border-right-color: transparent;
      border-radius: 16px;
      box-sizing: border-box;
      content: '';
      display: inline-block;
      height: 32px;
      left: 50%;
      margin: -16px 0 0 -16px;
      overflow: hidden;
      position: absolute;
      text-indent: -9999px;
      top: 50%;
      width: 32px;
      z-index: 101;
    }
  }

  &_loading {
    opacity: 0.2;
  }

  &_clickable {
    cursor: pointer;
    
    &:hover .header-stats-item__value{
      border-bottom: 1px solid #000;
    }
  }

  &__label {
    position: relative;
    color: #999;
    font: normal 12px "PT Sans Narrow";
    width: 60px;
    height: 50px;
    text-transform: none;
  }

  &__value {
    font: 700 30px "PT Sans Narrow";

    &_percentage:after {
      content: "%";
      display: inline-block;
      font-size: 17px;
      position: relative;
      top: -12px;
      color: #888;
    }
  }

  &__tooltip {
    display: flex;
    position: absolute;
    justify-content: center;
    align-items: center;
    left: 50%;
    bottom: -10px;
    z-index: 1000;
    margin-top: -5px;
    width: 110px;
    min-height: 25px;
    white-space: normal;
    background: #000;
    padding: 5px;
    border-radius: 3px;
    text-align: center;
    text-decoration: none;
    color: white;
    transform: translate(-50%, 100%);
    box-shadow: 0 3px 5px rgba(0,0,0,.2);
    transition: all .1s ease;
    pointer-events: none;
  
    &:hover {
      text-decoration: none;
      color: white;
    }
  
    &:before {
      content: "";
      display: block;
      position: absolute;
      bottom: 100%;
      left: 50%;
      transform: translateX(-50%);
      border-bottom: 5px solid #000;
      border-left: 5px solid transparent;
      border-right: 5px solid transparent;
    }
  }
}