.ui-popup {
  background: rgba(37, 37, 37, 0.95);
  border-radius: 5px;
  box-shadow: 0 0 7px;
  padding: 20px;
  position: absolute;
  z-index: 10000;

  .ui-popup-corner {
    background: transparent;
    border-left: 9px solid transparent;
    border-right: 9px solid transparent;
    height: 0;
    position: absolute;
    width: 0;
  }

  &.ui-popup_top-left,
  &.ui-popup_top-right {
    margin-top: 10px;

    .ui-popup-corner {
      border-bottom: 10px solid rgba(37, 37, 37, 0.95);
      bottom: 100%;
    }
  }
  &.ui-popup_bottom-left,
  &.ui-popup_bottom-right {
    margin-bottom: 10px;

    .ui-popup-corner {
      border-top: 10px solid rgba(37, 37, 37, 0.95);
      top: 100%;
    }
  }
  &.ui-popup_top-left,
  &.ui-popup_bottom-left {
    margin-left: -29px;

    .ui-popup-corner {
      left: 20px;
    }
  }
  &.ui-popup_top-right,
  &.ui-popup_bottom-right {
    margin-right: -29px;
    // transform: translateY(10px) translateX(157px);

    .ui-popup-corner {
      right: 20px;
    }
  }
}


