.dropdown-streams-container {
  min-width: 400px;
  background: #191919;
  border-radius: 8px;
  position: absolute;
  top: 50px;
  z-index: 100;
  padding: 20px 0;
  color: #EDEDED;
  display: block;

  &:after {
    content: '';
    height: 0;
    width: 0;
    border: 8px solid transparent;
    border-bottom-color: rgba(38,38,38,0.95);
    margin-left: -8px;
    position: absolute;
    top: -16px;
    left: 50%;
    transform: translateX(-50%);
  }

  &.small {
    left: -28px;
    width: 450px;

    &:after {
      left: 225px;
    }
  }

  &.large {
    left: -155px;

    &:after {
      left: 353px;
    }

    .dropdown-streams {
      width: 400px;
    }
  }

  .dropdown-streams-header {
    font-weight: 500;
    margin-bottom: 16px;
    text-align: center;

    span {
      color: #8C8C8C;
      padding: 0 5px;
      font-size: 12px;

      &:hover {
        cursor: pointer;
      }
      &.active {
        color: #EDEDED;
      }
    }
  }
  .dropdown-streams-content {

    .streams-content {
      display: none;
    }

    .streams-content.active {
      display: block;
    }

    .tables-count-1 {
      width: 100%;

      .dropdown-streams {
        width: 100%;
      }
    }

    .tables-count-2 {
      width: 810px;

      .dropdown-streams:last-child {
        margin-left: 10px;
      }
    }

    .tables-count-3 {
      width: 1220px;

      .dropdown-streams {
        &:last-child, &:nth-child(2) {
          margin-left: 10px;
        }
      }
    }
  }

  table.dropdown-streams {
    // width: 400px;
    float: left;
    font-size: 12px;

    & td:first-child {
      text-align: center;
    }

    tr {
      height: 29px;
      border-top: 1px solid #423F3F;
      opacity: 1;

      td {
        padding: 6px 6px;
        line-height: 0;

        &.streams-flag {
          text-align: center;
          width: 59px;

          img.streams-international-image {
            height: 16px;
          }
        }

        &.streams-tournament-name {
          width: 130px;
        }

        &.first-team {
          text-align: right;
          width: 96px;
        }
        &.second-team {
          text-align: left;
          width: 96px;
        }
        &.score {
          text-align: center;
          background-color: #323232;
          border-radius: 3px;
          width: 57px;

          &.is-streaming {
            background-color: #ED0000;
          }
        }

        a{
          white-space: nowrap;
        }
        a, a:hover {
          color: #EDEDED;
        }
        // &.status {
        //   text-align: right;
        //   padding-right: 30px;
        // }
        // &.action {
