.dropdown-menu-container {
  background: rgba(0,0,0,0.85);
  border-radius: 8px;
  display: none;
  min-width: 220px;
  position: absolute;
  right: 5px;
  top: 35px;
  z-index: 100;

  &:after {
    border-left: 8px solid transparent;
    border-right: 8px solid transparent;
    border-bottom: 8px solid rgba(0, 0, 0, 0.85);
    content: '';
    height: 0;
    right: 74px;
    margin-left: -8px;
    position: absolute;
    top: -8px;
    width: 0;
  }

  ul {
    margin-bottom: 0;

    li {
      cursor: pointer;

      a {
        border-bottom: 1px solid rgba(255,255,255,0.15);
        color: #fff;
        display: inline-block;
        font-size: 12px;
        padding: 12px 20px;
        text-decoration: none;
        width: 100%;

        &:hover {
          background: rgba(255, 255, 255, 0.2);
        }
      }

      &:first-child {
        a {
          border-radius: 8px 8px 0 0;
        }
      }

      &:last-child {
        a {
          border-radius: 0 0 8px 8px;
          border-bottom: none;
        }
      }

      &.profile-link{

        .profile-link-user-email{
          font-weight: bold;
          display: block;
          font-size: 1.15em;
          white-space: nowrap;
          text-overflow: ellipsis;
          overflow: hidden;
        }
        .profile-link-user-title{
          font-size: 80%;
          display: block;
        }
      }
    }

  }
}

.popup-fixed-container {
  background: rgba(38,38,38,0.9);
  display: none;
  height: 100%;
  left: 0;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1000;

  &.open {
    display: block;
  }

  &.statistics-match-map-popup {
    .statistics-match-list {
      padding-right: 600px;
    }
    .statistics-match-filter {
      margin-right: 600px;
    }
    .statistics-match-map-wrapper {
      width: 580px;
    }
    .statistics-match {
      min-height: 455px;
    }
  }

  .popup-fixed-inner {
    background: #fcfcfc;
    border-radius: 6px;
    box-shadow: 0 0 10px 0 rgba(0,0,0,0.3);
    overflow: hidden;
    position: absolute;
  }
  .popup-fixed-body {
    box-sizing: border-box;
    max-height: 100%;
    overflow: auto;
    padding: 43px 0 20px;
  }
  .popup-fixed-header {
    background: #476568;
    left: 0;
    margin: 0;
    padding: 14px 30px 14px 10px;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 10;
  }
  .popup-fixed-header-title {
    color: #fff;
    font-size: 13px;
    font-weight: normal;
    margin: 0;

    i {
      margin-right: 10px;
    }
  }
  .popup-fixed-close {
    display: inline-block;
    height: 20px;
    position: absolute;
    right: 10px;
    top: 10px;
    width: 20px;

    &:after,
    &:before {
      background: #fff;
      content: '';
      display: inline-block;
      height: 15px;
      left: 10px;
      position: absolute;
      top: 3px;
      width: 2px;
    }
    &:after {
      transform: rotate(-45deg);
    }
    &:before {
      transform: rotate(45deg);
    }
  }
  .popup-fixed-minimized {
    border-bottom: 2px solid #fff;
    display: inline-block;
    height: 17px;
    position: absolute;
    right: 35px;
    top: 10px;
    width: 14px;
  }

  &.popup-add-comment {
    .popup-fixed-inner {
      border: 1px solid #fff;
      height: 565px;
      left: 50%;
      margin: -282px 0 0 -430px;
      top: 50%;
      width: 860px;
    }
    .popup-fixed-player-title {
      color: #fff;
      font-size: 13px;
      font-weight: bold;
      left: 50%;
      margin: 0 0 0 -100px;
      position: absolute;
      text-align: center;
      top: 12px;
      width: 200px;
    }
    .popup-add-comment-select-for {
      display: inline-block;
      margin: 0 18px 0 0;
      width: 130px;
    }
    .popup-add-comment-select-comment,
    .popup-add-comment-select-template,
    .popup-add-comment-select-type {
      display: inline-block;
      margin: 0 4px;
      width: 165px;
    }
    .popup-add-comment-select-undefined {
      display: inline-block;
      margin: 0 0 0 18px;
      width: 130px;
    }
    .selectBox-dropdown {
      margin-bottom: 0;
    }
    .popup-add-comment-selects {
      border-bottom: 1px solid #dbe1e2;
      padding: 10px 18px;
    }
    .popup-add-comment-content {
      background: #f2f2f2;
      border-bottom: 1px solid #fff;
      border-top: 1px solid #fff;
      height: 394px;

      &:after {
        clear: both;
        content: '';
        display: block;
      }
    }
    .popup-add-comment-footer {
      border-top: 1px solid #dbe1e2;
      padding: 20px;
    }
    .popup-add-comment-content-main {
      display: inline-block;
      float: left;
      padding: 20px;
      width: 638px;
    }
    .popup-add-comment-content-sidebar {
      display: inline-block;
      float: right;
      padding: 20px 20px 20px 0;
      width: 218px;
    }
  }
}

.popup-container{

  .popup-button-space{
    height: 5px;
  }
  .ui-button {
    height: 30px;
    position: relative;
    display: block;
    line-height: 22px;
    margin-bottom: 5px;
    text-align: left;
    padding: 0 15px 0 30px;

    &:hover {
      background: #456164;
      background: linear-gradient(#ccc,#aaa);
      color: #000;
      text-decoration: none;
    }

    &:before, &:after {
      content: '';
      display: inline-block;
      width: 0;
      height: 100%;
      vertical-align: middle;
    }

    i {
      position: absolute;
      left: 5px;
      top: 50%;
      transform: translateY(-50%);
    }

    &__text {
      display: inline-block;
      width: 99%;
      vertical-align: middle;
      height: auto;
      white-space: normal;
    }
  }
}

