article.authorization {
  display: flex;
  width: 100%;
  height: 100vh;
  justify-content: center;
  align-items: center;
  background: url("/images/login-bg__basketball.jpg") center no-repeat;
  background-size: cover;

  .logo {
    background-repeat: no-repeat;
    background-size: contain;
    display: block;
    height: 24px;
    width: 100px;
  }

  .inner {
    display: flex;
    flex-direction: column;
    height: 85vh;
    justify-content: space-around;
    align-items: center;

    &.error {
      .block-content-title {
        background: #e70000;
        color: white;
      }
      .control {
        background: #ffe4e4;
      }
    }
  }

  .ui-button {
    background: #e9e9e9;
    border: 1px solid #fff;
    background: #f7f7f7;
    background: linear-gradient(to bottom, #f7f7f7 0%, #e0e0e0 100%);
    border-radius: 7px;
    box-shadow: 0 1px 3px -1px rgba(0, 0, 0, 0.8);
    color: #000000;
    display: inline-block;
    font-size: 13px;
    line-height: 13px;
    overflow: hidden;
    padding: 9px 5px;
    text-align: left;
    text-decoration: none;
    cursor: pointer;
    text-align: center;
  }
  .ui-control {
    width: 100%;
    height: 30px;
    border-radius: 5px;
    color: #1a1a1a;
    float: right;
    font-size: 13px;
    line-height: 13px;
    font-weight: bold;
    padding: 0;
    text-align: center;
    border: none;
    background-color: rgba(237, 237, 237, 0.5);

    &:focus {
      border: 1px solid #e1e1e1;
      border-top: 1px solid #cacaca;
      background-color: #EDEDED;

      &::placeholder {
        color: transparent;
      }
    }
  }

  .ui-control::placeholder {
    color: rgba(#1a1a1a, .7);
    font-family: "PT Sans", sans-serif;
    font-size: 10px;
    text-transform: uppercase;
    font-weight: 300;
    letter-spacing: 1px;
  }

  .block-content {
    display: flex;
    flex-direction: column;
    width: 350px;
    margin-bottom: 0;

    .block-content-title {
      background: white;
      border-radius: 10px 10px 0 0;
      color: #1a1a1a;
      font-size: 11px;
      height: 60px;
      line-height: 60px;
      text-transform: uppercase;
      margin: 0;
      font-weight: 600;
      letter-spacing: 1px;
      border-bottom: 1px solid rgba(#1a1a1a, .2);
      text-align: center;

      .block-content-toggle {
        display: inline-block;
        position: absolute;
        right: 10px;
        top: 11px;
      }
    }
    .block-content-body {
      border-radius: 0 0 10px 10px;
      padding: 0;
      background: white;
      border-bottom: 1px solid #c6c6c6;

      form {
        display: flex;
        flex-direction: column;
      }
    }
  }
  .control-wrapper {
    position: relative;
  }

  .modal-container {
    display: flex;
    flex-direction: column;
    height: 135px;
    justify-content: center;
    align-items: center;
  }

  .modal-row {
    width: 70%;
    margin: 7px 0;
  }

  .modal-aware {
    display: block;
    position: absolute;
    top: 110%;
    left: 50%;
    transform: translateX(-50%);
    font-size: 12px;
    line-height: 12px;
    color: #7c7a7a;
    white-space: nowrap;
    text-align: center;
  }

  .submit {
    height: 41px;
    margin: 10px auto 0;
    position: relative;
    width: 168px;
  }

  .modal-bottom {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 60px;
    border-top: 1px solid rgba(#1a1a1a, .2);
  }

  .modal-submit {
    display: block;
    height: 30px;
    padding: 0 30px;
    background-color: white;
    border: 1px solid rgba(#1a1a1a, .2);
    border-radius: 5px;
    color: rgba(#1a1a1a, .7);
    font-size: 11px;
    line-height: 12px;
    text-transform: uppercase;
    letter-spacing: 1px;

    &:hover {
      background: linear-gradient(180deg, #339595 0, darken(#339595, 5%));
      color: white;
      border: none;
    }
  }

  .icon-lock {
    left: 12px;
    position: absolute;
    top: 10px;
    z-index: 10;
    background-image: url("/images/icons/icon-lock__basketball.svg");
  }
  .icon-person {
    left: 10px;
    position: absolute;
    top: 10px;
    z-index: 10;
    background-image: url("/images/icons/icon-person__basketball.svg");
  }
  .problems {
    display: block;

    .problems-title {
      color: #7c7a7a;
      font-size: 13px;
      line-height: 13px;
      margin: 0 0 5px 0;
      text-align: center;
      font-weight: 700;
    }
    .problems-text {
      color: #7c7a7a;
      font-size: 13px;
      line-height: 13px;
      margin: 0;
    }
  }
  .contacts {
    white-space: nowrap;
  }
  .contact-link {
    color: #7c7a7a;
    font-size: 12px;

    .icon-skype {
      opacity: .7;
      margin: 0 5px 0 0;
    }
    .icon-phone {
      opacity: .7;
      margin: 0 2px 0 0;
    }
    .icon-email {
      opacity: .7;
      margin: 3px 5px 0;
    }
  }
  .col-xs {
    width: auto;
    display: inline-block;
    padding: 0;
    a{
      text-decoration: none;
      padding: 0 20px;
      &:hover{
        text-decoration: underline;
      }
    }
  }
}

.wanna-try {
  display: block;
  position: relative;
  width: 470px;
  margin: auto;
  min-width: 220px;
  box-shadow: 0 0 5px rgba(247,238,180,0.4);
  text-align: center;
  padding: 20px;
  background: rgba(0,0,0,0.85);
  border-radius: 8px;
  z-index: 100;

  &_loader {

    &:before{
      content: '';
      display: block;
      position: absolute;
      background: rgba(255, 255, 255, 0.85);
      z-index: 100;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
    }
    &:after{
      animation: three-quarters 1250ms infinite linear;
      border: 8px solid #A7A7A7;
      border-right-color: transparent;
      border-radius: 16px;
      box-sizing: border-box;
      content: '';
      display: inline-block;
      height: 32px;
      left: 50%;
      margin: -16px 0 0 -16px;
      overflow: hidden;
      position: absolute;
      text-indent: -9999px;
      top: 50%;
      width: 32px;
      z-index: 101;
    }
  }

  &__wrapper {
    display: flex;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(white, .7);
    z-index: 1000;
  }

  &__title {
    color: white;
    font-size: 18px;
    font-weight: 700;
    margin: 0;
  }

  &__form {
    margin-top: 20px;
    margin-bottom: 0;
  }

  &__fieldset {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-top: 0;
    margin-bottom: 10px;

    &_small {
      margin-right: 135px;

      .wanna-try__required-select {
        right: -50px;
      }
    }

    &_country {
      .wanna-try__fake-select {
        width: 300px;
      }
      .wanna-try__select-bottom {
        width: 300px;
      }
    }
  }

  &__label {
    text-align: right;
    color: white;
    font-size: 12px;
    font-weight: 400;
    position: relative;
  }

  &__required {
    display: block;
    position: absolute;
    top: 6px;
    left: 152px;
    color: rgba(white, .7);
    pointer-events: none;
  }

  &__required-select {
    display: block;
    position: absolute;
    top: 5px;
    right: 25px;
    color: rgba(white, .7);
  }

  &__name {
    width: 300px;
    height: 30px;
    margin-left: 20px;
    box-sizing: border-box;
    background-color: #4d4d4d;
    background-image: linear-gradient(180deg, rgba(73,73,73,0.49) 0%, rgba(95,95,95,0.52) 100%);
    box-shadow: 0 1px 0 rgba(255,255,255,0.3), inset 0 1px 0 rgba(0,0,0,0.2);
    padding-left: 20px;
    border: none;
    border-radius: 6px;
    font-size: 13px;
    line-height: 13px;
    font-family: "PT Sans", sans-serif;
    color: #fff;

    &:hover {
      background-color: #626262;
    }

    &_error {
      box-shadow: none;
      border: 3px solid red;
    }
  }

  &__helper {
    width: 300px;
    text-align: left;
  }

  &__select {
    display: none;
  }

  &__select-bottom {
    display: block;
    position: absolute;
    width: 165px;
    min-width: 152px;
    max-height: 200px;
    min-height: 1em;
    top: 31px;
    left: 130px;
    margin: 0;
    z-index: 99999;
    overflow: auto;
    padding: 0;
    list-style: none;
    cursor: pointer;
    border-radius: 6px;
    background-image: linear-gradient(180deg, rgba(73, 73, 73, 0.49) 0%, rgba(95, 95, 95, 0.52) 100%);
    border: none;
    box-shadow: 0 0 5px rgba(247, 238, 180, 0.4);
    background-color: #4d4d4d;

    &_selected {

    }

    li {
      display: block;
      margin: 0;
      padding: 0;
      cursor: pointer;
      list-style: none;

      a {
        display: block;
        margin: 0;
        padding: 4px 25px 4px 10px;
        overflow: hidden;
        cursor: pointer;
        list-style: none;
        line-height: 2;
        text-align: right;
        text-decoration: none;
        white-space: nowrap;
        background: 6px center no-repeat;
        background-color: #464646;
        font-size: 11px;
        color: #fff;
        text-overflow: ellipsis;

        &:hover {
          background-color: #476568;
        }
      }
    }
  }

  &__select-bottom_job {
    top: 270px;
  }

  &__select-bottom_position {
    top: 315px;
  }

  &__fake-select {
    position: relative;
    width: 165px;
    height: 30px;
    padding-left: 20px;
    margin-left: 20px;
    cursor: pointer;
    outline: none;
    border: none;
    border-radius: 6px;
    background-color: #4d4d4d;
    font-size: 11px;
    text-align: left;

    &:hover {
      background-color: #626262;
    }

    &_error {
      border: 3px solid red;

      .wanna-try__arrow-icon {
        top: 10px;
      }
    }
  }

  &__fake-placeholder {

  }

  &__arrow-icon {
    border-top: 5px solid #b3b3b3;
    border-left: 5px solid transparent;
    border-right: 5px solid transparent;
    position: absolute;
    top: 12px;
    right: 10px;
  }

  &__button-wrapper {
    height: 40px;
  }

  &__button {
    margin: auto;
    width: 170px;
    height: 30px;
    background-color: rgba(252, 252, 252, 0.4);
    border: none;
    border-radius: 6px;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.5), inset 0 -1px 0 rgba(0, 0, 0, 0.18);
    color: #fcfcfc;
    font-size: 12px;
    line-height: 12px;
    -webkit-appearance: none;

    &:hover {
      background-color: #4d4d4d;
    }
  }
}

.wanna-try-trial {
  display: flex;
  position: relative;
  border: 1px solid #9A9A9A;
  border-radius: 10px;
  background-image: url('/images/free.png');
  background-size: auto;
  background-repeat: no-repeat;
  height: 45px;
  min-width: 180px;
  cursor: pointer;

  &:hover {
    .wanna-try-trial__text {
      text-decoration: underline;
    }
  }

  &__text {
    display: block;
    position: relative;
    margin: auto;
    font-size: 14px;
    line-height: 14px;
    font-weight: 700;
    color: #7c7a7a;
  }
}

.wanna-try-success {
  display: flex;
  position: absolute;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 470px;
  margin: auto;
  min-width: 220px;
  box-shadow: 0 0 5px rgba(247,238,180,0.4);
  text-align: center;
  padding: 20px;
  background: rgba(0,0,0,0.85);
  border-radius: 8px;
  z-index: 100;

  &__text {
    display: flex;
    position: relative;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
    font-size: 16px;
    font-weight: 700;
  }

  &__thx {
    display: block;
    position: relative;
    padding: 20px;
  }

  &__info {
    display: block;
    position: relative;
    padding: 20px;
    line-height: 32px;
  }

  &__button {
    display: block;
    position: relative;
    background-color: hsla(0,0%,99%,.4);
    border-radius: 6px;
    box-shadow: 0 1px 2px rgba(0,0,0,.5), inset 0 -1px 0 rgba(0,0,0,.18);
    color: white;
    font-size: 13px;
    line-height: 13px;
    padding: 10px 70px;
    margin: 20px 0;
    cursor: pointer;
  }
}
