@mixin laptop {
  @media screen and (max-width: 1360px) {
    @content
  }
}

article.player {

  .ui-button {
    &.button-player-video {
      font-size: 13px;
      height: 40px;
      margin: 3px 6px;
      padding: 15px 43px 11px 29px;
      position: relative;
      text-align: left;
      white-space: nowrap;
      width: 165px;
      vertical-align: top;

      i {
        left: 7px;
        margin-top: -8px;
        position: absolute;
        top: 50%;
      }
      .button-player-video-number {
        border-left: 1px solid rgba(0,0,0,0.1);
        color: #000000;
        display: inline-block;
        margin-top: -14px;
        padding: 9px 0 7px;
        position: absolute;
        right: 0px;
        text-align: center;
        top: 50%;
        width: 40px;

        &:after {
          background: rgba(255,255,255,0.1);
          content: '';
          display: inline-block;
          height: 100%;
          left: 0;
          position: absolute;
          top: 0;
          width: 1px;
        }
      }

      &.two-rows {
        padding: 8px 43px 8px 29px;
        white-space: normal;
      }

      .button-player-video-text {
        padding-left: 0;
        position: absolute;
        left: 34px;
        right: 47px;
        white-space: normal;
        top: 50%;
        transform: translateY(-50%);

        span {
          padding-left: 0;
        }
      }
    }

    &.button-with-icon-dot {
      text-align: center;
      width: 30px;

      i {
        margin: 3px;
      }
    }
    &.button-with-icon-diagram {
      padding: 7px 4px;
    }
    &.button-with-icon-selection {
      padding: 7px 8px 6px 7px;

      i {
        margin: 1px 0 0 1px;
      }
    }
    &.button-with-icon-zoom {
      padding: 9px 12px;

      i {
        margin-top: -2px;
        margin-left: -2px;
      }
    }
    &.button-match-video {
      display: block;
      line-height: 22px;
      margin-bottom: 5px;
      text-align: left;

      i {
        margin: 2px 4px 0 2px;
      }
      .count {
        border-radius: 10px;
        color: #476568;
        display: inline-block;
        float: right;
        font-size: 11px;
        line-height: 12px;
        margin: 1px;
        min-width: 30px;
        padding: 4px 5px;
        text-align: center;
      }

      &:hover,
      &:focus,
      &:active {
        text-decoration: none;
      }
    }

    &.button-smart-search {
      &:hover {
        background: #007f3e;
        box-shadow: 0px -1px 0px 0px rgba(0,0,0,0.2), 0px 0px 1px 0px rgba(0,0,0,0.2);
        color: #fff;
      }
    }
  }

  .table-career {
    th.table-career-param-column {
      text-transform: uppercase;
    }
    tr td:nth-child(2) {
      color: #bc0000;
    }
  }

  .player-empty-matches {
    color: #1a1a1a;
    padding-top: 15px;
  }

  .player-card {
    .container-fluid {
      text-align: center;
    }
  }
  .player-card-wrapper {
    height: 144px;
  }
  .player-card-responsive {
    border-left: 500px solid transparent;
    width: 100%;
  }
  .player-card-left {
    float: left;
    width: 480px;
  }

  .player-card-video {
    display: inline-block;
    float: right;
    padding: 25px 40px 0 0;
    text-align: right;
    vertical-align: top;
    width: 753px;

    .player-card-video-row {
      .button-player-video {
        &:last-child {
          margin-right: 0;
        }
      }
    }
  }
  .player-card-stat {
    float: left;
    width: 220px;
  }

  .player-card {
    padding: 0 13px;
    position: relative;

    &:after {
      clear: both;
      content: '';
      display: block;
    }

    .player-column {
      display: inline-block;
      float: left;

      &.player-column-avatar {
        margin-right: 5px;
      }
      &.player-column-name {
        max-width: 290px;
        text-align: left;
      }
      &.player-column-video {
        padding-top: 10px;
      }
      &.player-column-links {
        margin-right: 0;
        padding-left: 9px;
        padding-top: 10px;
      }
    }

    .player-card-avatar {
      display: inline-block;
      margin: 0px 0 -5px 0;
      overflow: hidden;
      padding-left: 30px;
      padding-right: 30px;
      position: relative;
      width: 180px;

      img {
        height: 120px;
        margin-top: 24px;
        position: relative;
        z-index: 2;
      }
    }

    .player-card-params {
      color: #1a1a1a;
      font-size: 11px;
      margin-top: 25px;
      max-width: 100px;
      white-space: nowrap;
    }

    .player-card-title {
      color: #1a1a1a;
      font-size: 18px;
      font-weight: bold;
      max-height: 50px;
      line-height: 18px;
      margin: 35px -20px 10px 0;
      overflow: hidden;
      text-overflow: ellipsis;

      &.player-card-title_small {
        font-size: 22px;
      }
    }
    .player-card-team {
      color: #1a1a1a;
      font-size: 12px;

      a {
        color: #1a1a1a;
        font-size: 12px;
      }
    }
    .player-card-contract {
      color: #fff;
      font-size: 11px;
    }
    .player-card-legs {
      color: #fff;
      font-size: 11px;
      margin-top: 13px;
      padding-left: 34px;
      position: relative;

      .player-card-legs-icon {
        height: 23px;
        left: 0;
        position: absolute;
        top: 2px;
        width: 24px;
      }
      .player-card-legs-text {
        margin-bottom: 0;
      }
    }

    .player-card-statistics {
      margin: 10px 0 15px;
      position: relative;

      &:after {
        clear: both;
        content: '';
        display: block;
      }

      .player-card-statistics-item {
        border-right: 1px solid rgba(255,255,255,0.2);
        color: #fff;
        font-family: "Arial Black", sans-serif;
        font-size: 18px;
        padding-right: 10px;
        padding-left: 31px;
        position: relative;
        white-space: nowrap;

        i {
          left: 10px;
          position: absolute;
          top: 12px;

          &.icon-index {
            left: 0px;
            top: 9px;
          }
          &.icon-matches {
            left: 17px;
            top: 7px;
          }
          &.icon-cards {
            left: 12px;
          }
          &.icon-minutes {
            top: 7px;
          }
          &.icon-assists {
            left: 12px;
          }
          &.icon-goals {
            left: 4px;
          }
        }
      }

      .player-card-statistics-row {
        &:first-child {
          .player-card-statistics-item {
            padding-bottom: 5px;
            padding-top: 0px;
          }
        }
        &:last-child {
          .player-card-statistics-item {
            border-bottom: none;
            padding-bottom: 0px;
            padding-top: 5px;
          }
        }

        .player-card-statistics-item {
          border-bottom: 1px solid rgba(255,255,255,0.2);

          &:last-child {
            border-right: none;
            padding-right: 0;
          }
          &:first-child {
            padding-left: 21px;
          }
        }
      }
    }

    .player-card-position {
      color: #fff;
      font-size: 11px;
      width: 204px;

      tr {
        td {
          &:last-child {
            padding-left: 20px;
            text-align: right;
          }
        }
      }
    }

    .player-card-links {
      margin-top: 4px;
      white-space: nowrap;
      color: black;
      cursor: pointer;

      .players-info{

        padding: 0 14px 0 17px;
        position: relative;
        display: inline-block;
        height: 17px;
        line-height: 17px;
        margin-top: 1px;

        .icon-player-logo:before,
        .icon-player-logo:after{
          width: 15px;
          height: 15px;
          position: absolute;
          display: inline-block;
          left: 0;
          bottom: 2px;
        }
        .icon-player-logo:before{
          content: url('/images/icons/svg/icon-player-logo.svg');
        }
        .icon-player-logo:after{
          opacity: 0;
          content: url('/images/icons/svg/icon-player-logo-hover.svg');
        }


        .icon-player-matches:before,
        .icon-player-matches:after{
          width: 9px;
          height: 12px;
          position: absolute;
          display: inline-block;
          left: 0;
          bottom: 4px;
        }
        .icon-player-matches:before{
          content: url('/images/icons/svg/icon-player-matches.svg');
        }
        .icon-player-matches:after{
          opacity: 0;
          content: url('/images/icons/svg/icon-player-matches-hover.svg');
        }


        .icon-player-goals:before,
        .icon-player-goals:after{
          width: 13px;
          height: 13px;
          position: absolute;
          display: inline-block;
          left: 0;
          bottom: 3px;
        }
        .icon-player-goals:before{
          content: url('/images/icons/svg/icon-player-goals.svg');
        }
        .icon-player-goals:after{
          opacity: 0;
          content: url('/images/icons/svg/icon-player-goals-hover.svg');
        }


        .icon-player-shape:before,
        .icon-player-shape:after{
          width: 17px;
          height: 17px;
          position: absolute;
          display: inline-block;
          left: -1px;
          bottom: 0;
        }
        .icon-player-shape:before{
          content: url('/images/icons/svg/icon-player-shape.svg');
        }
        .icon-player-shape:after{
          opacity: 0;
          content: url('/images/icons/svg/icon-player-shape-hover.svg');
        }

        &:hover{
          i:after{
            opacity: 1;
          }
        }

        &:last-child{
          margin-right: 30px;
        }

      }

      .player-card-link {
        display: inline-block;
        margin: 2px 6px;
        vertical-align: top;

        &:first-child {
          margin-left: 0;
        }
        &.player-card-link_subscribe {
          margin-top: 4px;
        }
        &.player-card-link_message{
          margin-top: 0;
        }
        &.player-card-link_pdf{
          margin-top: 3px;
        }
        &:hover,
        &.active {
          .icon-subscribe_gray {
            background-image: url('/images/icons/svg/icon-subscribe-hover.svg');
          }
          .icon-favorite_gray {
            background-image: url('/images/icons/svg/icon-favorite-hover.svg');
          }
          .icon-search_gray {
            background-image: url('/images/icons/svg/icon-search-hover.svg');
          }
          .icon-message_gray {
            background-image: url('/images/icons/svg/icon-message-hover.svg');
          }
          .icon-pdf {
            background-image: url('/images/icons/svg/icon-pdf-hover.svg');
          }
        }
      }
    }

    .player-card-top-stat {
      display: inline-block;
      margin: 30px 0 0;
      white-space: nowrap;

      .progress-radial-wrapper {
        display: inline-block;
        margin-left: 15px;
        vertical-align: top;

        &:first-child {
          margin-left: 0px;
        }
        &:hover {
          .progress-radial-title {
            display: block;
          }
          .progress-radial{
            box-shadow: 1px 1px 7px 1px rgba(0,0,0,0.2);
            .overlay {
              box-shadow: inset 1px 1px 7px 1px rgba(0,0,0,0.2);
            }
          }
        }
      }
      .progress-radial-title {
        color: #1a1a1a;
        display: none;
        font-size: 10px;
        font-weight: bold;
        line-height: 9px;
        margin: 0 0 0 0;
        text-align: center;

        &.progress-radial-title_isi {
          color: #bc0000;
        }
      }
    }
  }

  .player-details {
    box-shadow: inset 0 5px 6px 1px rgba(0, 0, 0, 0.1);
    padding: 25px 13px 0;
    position: relative;
  }
  .player-details-responsive {
    padding-left: 700px;
    width: 100%;
    float: right;
  }
  .player-details-left {
    width: 780px;
    position: absolute;
    left: 0;
    top: 0;
  }

  .goalkeeper-details-right {
    width: 600px;
    float: right;
    position: absolute;
    right: 0;
  }
  .goalkeeper-details-left {
    padding-right: 600px;
    float: left;
    position: relative;
    width: 100%;
    .skills-list{
    }
  }

  .player-details-wrapper {
    position: relative;
    background: #f2f2f2;
    border-radius: 5px;
    border-bottom: 1px solid #c6c6c6;
    border-top: 1px solid #fefefe;

    &:after {
      clear: both;
      content: '';
      display: block;
    }
  }

  .player-field {
    background-image: url("/images/football-field2.png");
    background-position: center;
    background-repeat: no-repeat;
    display: inline-block;
    float: left;
    height: 166px;
    margin: 20px 10px 12px;
    width: 165px;
  }

  .player-details-info {
    color: #1a1a1a;
    float: left;
    font-size: 11px;
    padding: 20px 14px 0 14px;
    width: 210px;
    height: 200px;
    position: relative;

    &:after {
      background: #FFF;
      border-right: 1px solid #dbe1e2;
      content: '';
      display: inline-block;
      position: absolute;
      right: -1px;
      top: 0;
      bottom: 0px;
      width: 2px;
    }

    .player-details-info-row {
      &:after {
        clear: both;
        content: '';
        display: block;
      }

      .player-details-info-column-left {
        float: left;

        .icon-video-player-play {
          margin: 3px 3px 0 0;
        }
      }
      .player-details-info-column-right {
        float: right;
      }
    }
    .flag-icon {
      margin-left: 5px;
    }
    .player-card-position {
      margin-top: 30px;
    }
  }

  .player-card .player-details-info {
    padding: 25px 20px 15px 30px;
    font-size: 12px;
    width: 230px;
    height: 140px;
    bottom: 0;
  }


  .matches-switcher {
    color: #fff;
    display: inline-block;
    height: 25px;
    left: 50%;
    margin-left: -150px;
    position: absolute;
    text-align: center;
    top: 5px;
    width: 300px;

    .matches-switcher-label {
      display: inline-block;
      float: left;
      padding: 8px;
      width: 120px;

      &:first-child {
        text-align: right;
      }
      &:last-child {
        text-align: left;
      }
    }
    .matches-tumbler {
      background: #fff;
      border-radius: 10px;
      box-shadow: inset 1px 1px 8px 1px rgba(0, 0, 0, 0.5);
      cursor: pointer;
      display: inline-block;
      float: left;
      height: 20px;
      margin: 6px 4px;
      padding: 0 5px;
      position: relative;
      width: 40px;

      .matches-tumbler-btn {
        background: #82bc55;
        border-radius: 50%;
        box-shadow: 0 1px 1px 1px rgba(0, 0, 0, 0.5);
        content: '';
        display: inline-block;
        height: 13px;
        left: 5px;
        position: absolute;
        top: 3px;
        width: 13px;
      }
      &.state-only-video {
        &.matches-tumbler-btn {
          left: auto;
          right: 5px;
        }
      }
    }
  }


  .similar-column {
    display: inline-block;
    padding: 0 10px;
    width: 49.99%;
    vertical-align: top;

    &:first-child {
      border-right: 1px solid #fff;
    }
    &:last-child {
      border-left: 1px solid #dbe1e2;
    }

    .similar-row {
      margin-bottom: 5px;

      &:last-child {
        margin-bottom: -4px;
      }
      &:after {
        clear: both;
        content: '';
        display: block;
      }
      .similar-flag {
        float: left;
        margin-right: 9px;
      }
      .similar-name {
        color: #1a1a1a;
        display: inline-block;
        float: left;
        font-size: 11px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        width: 88px;
      }
      .similar-team {
        color: #8ea3a6;
        display: inline-block;
        float: left;
        font-size: 10px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        width: 55px;
      }
    }
  }

  .compare-switcher {
    width: 200px;
  }
  .compare-block {
    border-bottom: 1px solid #dbe1e2;
    border-top: 1px solid #ffffff;
    min-height: 151px;
    padding: 10px 0;
    position: relative;

    &.loading {
      &:before {
        background: none;
      }
    }

    .compare-title {
      color: #1a1a1a;
      font-size: 14px;
      font-weight: bold;
      margin: 0;
    }
    .compare-place {
      background: #d9d9d9;
      display: inline-block;
      font-size: 10px;
      height: 13px;
      letter-spacing: -0.5px;
      text-align: center;
      width: 15px;
    }

    &:first-child {
      border-top: none;
    }
    &:last-child {
      border-bottom: none;
    }
    .compare-more {
      float: right;
      margin-top: 3px;
      padding: 0;
        border: none;
        background-color: transparent;
        outline: none;
    }
    .compare-form {
      float: right;
    }
  }
  .table-compare {
    margin: 10px 0 0 0;

    &.table {
      > tbody {
        > tr {
          > td {
            border-top: none;
            color: #1a1a1a;
            font-size: 11px;
            padding: 0px 3px;

            a {
              color: #1a1a1a;
              font-size: 11px;
            }

            &.table-compare-column-place {
              width: 20px;
            }
            &.table-compare-column-name {
              width: 135px;

              @include laptop {
                overflow: hidden;
                text-overflow: ellipsis;
                max-width: 69px;
              }

              a {
                display: inline-block;
                line-height: 17px;
                max-width: 85px;
                overflow: hidden;
                position: relative;
                text-overflow: ellipsis;
                white-space: nowrap;
              }
            }
            &.table-compare-column-index {
              text-align: center;
              width: 55px;
            }
          }

          &.highlight {
            > td {
              &.table-compare-column-name,
              &.table-compare-column-index {
                font-weight: bold;
              }
            }
          }
        }
      }
    }
    .table-compare-percents {
      color: #8ea3a6;
      font-size: 10px;
      margin-left: 5px;
    }
  }

  .table-matches {
    tr:hover {
      td {
        background: #ccc;
      }
    }

    .table-matches-param-column, .table-matches-index-column {
      &:hover {
        font-weight: 600;

        .table-matches-procent {
          font-weight: 600;
        }
      }
    }

    thead{
      .table-matches-param-column{
        word-break: break-word;
        -webkit-hyphens: auto;
        -moz-hyphens: auto;
        -ms-hyphens: auto;
        -o-hyphens: auto;
        hyphens: auto;
      }
    }

    tbody {
      .table-matches-param-column > * {
        text-align: center;
        margin: auto;
        float: left;
        width: 100%;
        min-width: 45px;
      }
    }

  }

  .player-header {
    background: #476568;
    box-shadow: 0 4px 5px 2px rgba(0,0,0,0.2);
    display: inline-block;
    height: 50px;
    left: 0;
    padding: 0 13px;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1000;

    .logo {
      float: left;
      margin: 17px 0;
    }
    .player-header-avatar {
      float: left;
      position: relative;
      width: 45px;

      &:after {
        box-shadow: 0 0 20px 18px rgba(0, 0, 0, 0.5);
        content: '';
        display: inline-block;
        height: 0px;
        left: 50%;
        position: absolute;
        top: 70%;
        width: 0px;
        z-index: 1;
      }

      img {
        margin-top: 5px;
        height: 45px;
        position: relative;
        z-index: 2;
      }
    }
    .player-header-title {
      color: #fff;
      font-size: 14px;
      font-weight: bold;
      margin: 0;
    }
    .player-header-name {
      float: left;
      padding: 12px 0 0 25px;
      width: 295px;
    }
    .player-header-team {
      a {
        color: #8ea3a6;
        font-size: 11px;
      }
    }
    .player-header-table {
      float: right;
      margin-right: 249px;

      &.large {
        margin-right: 249px;
      }
      ul {
        border-left: 1px solid #748a8d;
        border-right: 1px solid #3d595c;
        height: 50px;
        margin: 0;
      }

      li {
        border-left: 1px solid #3d595c;
        border-right: 1px solid #748a8d;
        color: #fff;
        float: left;
        font-size: 9px;
        font-weight: normal;
        height: 50px;
        line-height: 9px;
        padding: 16px 4px 0;
        text-align: center;
        vertical-align: top;
        width: 50px;
        word-break: break-word;
        -webkit-hyphens: auto;
        -moz-hyphens: auto;
        -ms-hyphens: auto;
        -o-hyphens: auto;
        hyphens: auto;

        &.player-header-instat-index {
          // color: #bc0000;
        }
      }
    }

    .player-header-table_wrc {
      margin-right: 0!important;
    }

    .player-header-table_wrc {
      margin-right: 0;
    }

    .compare-switcher {
      position: absolute;
      right: 10px;
      top: 8px;
    }
  }

  .content-column-left {
    .block-content_compare {
      .block-content-body {
        padding: 0;
      }

      .compare-switcher {
        width: 200px;
      }
      .compare-block {
        display: inline-block;
        float: left;
        min-height: 154px;
        padding: 10px;
        position: relative;
        width: 49%;
        border-top: none;
        border-bottom: none;

        &:nth-child(even) {
          border-left: 1px solid #ffffff;
        }
        &:nth-child(odd) {
          border-right: 1px solid #dbe1e2;
        }

        &.loading {
          &:before {
            background: none;
          }
        }

        .compare-title {
          color: #1a1a1a;
          font-size: 14px;
          font-weight: bold;
          margin: 0;
          padding-right: 20px;
          position: relative;
        }
        .compare-place {
          background: #d9d9d9;
          display: inline-block;
          font-size: 10px;
          height: 13px;
          letter-spacing: -0.5px;
          text-align: center;
          width: 15px;
        }

        &:first-child {
          border-top: none;
        }
        &:last-child {
          border-bottom: none;
        }
        .compare-key {
          display: inline-block;
          overflow: hidden;
          white-space: nowrap;
          width: 100%;
        }
        .compare-more {
          float: right;
          margin-top: 3px;
          padding: 0;
          border: none;
          background-color: transparent;
          outline: none;
          position: absolute;
          top: 0;
          right: 0;
        }
        .compare-form {
          float: right;
        }
      }
      .table-compare {
        margin: 10px 0 0 0;

        &.table {
          > tbody {
            > tr {
              > td {
                border-top: none;
                color: #1a1a1a;
                font-size: 11px;
                padding: 0px 3px;

                a {
                  color: #1a1a1a;
                  font-size: 11px;
                }

                &.table-compare-column-place {
                  width: 20px;
                }
                &.table-compare-column-name {
                  width: 135px;

                  a {
                    display: inline-block;
                    line-height: 17px;
                    max-width: 135px;
                    overflow: hidden;
                    position: relative;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                  }
                }
                &.table-compare-column-name_percent {
                  width: 95px;

                  a {
                    max-width: 95px;
                  }
                }
                &.table-compare-column-index {
                  text-align: center;
                  width: 55px;
                }
              }

              &.highlight {
                > td {
                  &.table-compare-column-name,
                  &.table-compare-column-index {
                    font-weight: bold;
                  }
                }
              }
            }
          }
        }
        .table-compare-percents {
          color: #8ea3a6;
          font-size: 10px;
          margin-left: 5px;
        }
      }
    }
  }
}

@for $i from 0 through 20 {
  $width: 1040 + $i * 50;
  @media screen and (max-width: #{$width}px) {
    article.player {
      .player-header-table-#{$width} {
        display: none;
      }
      .player-header-table.large {
        .player-header-table-#{$width} {
          display: inline-block;
        }
      }
    }
  }
  @media screen and (max-width: #{$width - 300}px) {
    article.player {
      .player-header-table.large {
        .player-header-table-#{$width} {
          display: none;
        }
      }
    }
  }
}

@for $i from 0 through 21 {
  $width: 940 + $i * 50;
  @media screen and (max-width: #{$width}px) {
    .table-matches-max-width-#{$width} {
      display: none;
    }
    .block-content_matches.large {
      .table-matches-max-width-#{$width} {
        display: table-cell;
      }
    }
  }
  @media screen and (max-width: #{$width - 300}px) {
    .block-content_matches.large {
      .table-matches-max-width-#{$width} {
        display: none;
      }
    }
  }
}


@media screen and (max-width: 1100px) {
  article.player {
    .block-content_matches {
      .diagram-container {
        width: 100%;

        &:last-child {
          display: none;
        }
      }
    }
  }
}

@media screen and (max-width: 1160px) {
  article.player {
    .player-card-video {
      padding-right: 0;
    }
  }
}

@media screen and (max-width: 1112px) {
  article.player {
    .player-card-video {
      padding-right: 0;
      width: 625px;

      .button-player-video {
        &.button-player-video_EpisodesSearch,
        &.button-player-video_TeamPlaylists {
          width: 80px;

          .button-player-video-text,
          .icon-video {
            display: none;
          }

          &:after {
            background-repeat: no-repeat;
            background-size: 16px;
            content: '';
            display: inline-block;
            height: 19px;
            left: 13px;
            position: absolute;
            top: 9px;
            vertical-align: top;
            width: 16px;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1000px) {
  article.player {
    .player-card-video {
      padding-right: 0;
      width: 374px;

      .button-player-video {
        &.button-player-video_BestEpisodes,
        &.button-player-video_WorstEpisodes,
        &.button-player-video_Goals,
        &.button-player-video_Assists,
        &.button-player-video_FullMatches,
        &.button-player-video_Summary {
          width: 80px;

          .button-player-video-text,
          .icon-video {
            display: none;
          }

          &:after {
            background-repeat: no-repeat;
            background-size: 16px;
            content: '';
            display: inline-block;
            height: 19px;
            left: 13px;
            position: absolute;
            top: 9px;
            vertical-align: top;
            width: 16px;
          }
        }

        &.button-player-video_BestEpisodes {
          &:after {
            background-image: url('/images/icons/icon-like.png');
          }
        }
        &.button-player-video_WorstEpisodes {
          &:after {
            background-image: url('/images/icons/icon-dislike.png');
          }
        }
        &.button-player-video_Goals {
          &:after {
            background-image: url('/images/icons/svg/icon-goal.svg');
          }
        }
        &.button-player-video_Assists {
          &:after {
            background-image: url('/images/icons/svg/icon-assists-light-blue.svg');
          }
        }
        &.button-player-video_FullMatches {
          &:after {
            background-image: url('/images/icons/svg/icon-matches-light-blue.svg');
          }
        }
        &.button-player-video_Summary {
          &:after {
            background-image: url('/images/icons/svg/icon-diagram.svg');
          }
        }
      }
    }
  }
}
