.live-search {
  position: relative;
  min-width: 200px;

  .header-search-button {
    outline: none;
  }
}

.live-search-results {
  position: absolute;
  width: 104%;
  top: -10px;
  left: -2%;
  padding: 54px 0 0;
  border-radius: 6px;
  border: 1px solid #c6c6c6;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.3);
  pointer-events: none;

  &__header {
    display: flex;
    width: 100%;
    height: 30px;
    padding: 0 15px;
    align-items: center;
    background-color: #dbf0ef;

    i {
      margin-right: 6px;
      position: relative;
      opacity: .4;
    }

    span {
      color: rgba(#1a1a1a, 0.7);
      text-transform: uppercase;
      font-weight: normal;
      font-size: 10px;
      line-height: 11px;
      letter-spacing: 1px;
    }
  }

  span.live-search-results__name, span.live-search-results__team {
    display: block;
    width: 50%;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  span.live-search-results__name {
    padding-right: 15px;
  }

  span.live-search-results__team {
    padding-right: 0;
    color: rgba(26, 26, 26, 0.5);
  }
}

.live-search-results_hidden {
  display: none;
}

.live-search-results-panel {
  overflow-y: auto;
  margin-bottom: 0;
  max-height: calc(100vh - 80px);
  pointer-events: all;
}

.live-search-results-panel-header {
  position: relative;
  color: #1a1a1a;
  padding: 0;
  font-weight: 600;
}

.live-search-results-panel-header_players {
  &:before {
    background: url("/images/blocks/live_search/")
  }
}

.live-search-results-panel-list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.live-search-results-panel-list-item {
  margin: 0;
  color: black;
  font-size: 12px;
  line-height: 12px;
  position: relative;
  white-space: nowrap;
  list-style: none;
  cursor: pointer;

  .live-search__gender {
    position: absolute;
    right: 17px;
    display: block;
    top: 50%;
    font-weight: normal;
    transform: translateY(-50%);

    &_women {
      color: red;
    }

    &_men {
      color: blue;
    }
  }

  a {
    display: flex;
    width: 100%;
    height: 30px;
    align-items: center;
    color: #1a1a1a;
    text-decoration: none;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    padding: 0 36px;
    background-color: white;

    span {
      font-weight: normal;
      font-size: 12px;
      line-height: 12px;
    }

    &:hover {
      background-color: #f2f2f2;
    }
  }
}

.live-search-page-section {
  margin-bottom: 50px;

  &:last-child {
    margin-bottom: 0;
  }
}

.live-search-page-section-header {
  color: #1a1a1a;
  margin-top: 18px;
  margin-bottom: 12px;
  font-size: 36px;
  line-height: 36px;
}

.live-search-page-section-footer {
  text-align: center;
  padding: 16px 0;
}

.live-search-page-item-link {
  &:hover, &:focus {
    text-decoration: none;
  }
}

.live-search-page-player {
  cursor: pointer;
  position: relative;
  margin-bottom: 10px;
  background-color: #f2f2f2;
  color: #1a1a1a;
  border-radius: 6px;
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.3);

  &:hover {
    background-color: #dadada;
  }
}

.live-search-page-player__inner {
  padding: 8px 8px 8px 80px;
  height: 75px;
}

.live-search-page-player__image {
  position: absolute;
  bottom: 0;
  left: 7px;
  width: 64px;
  height: 64px;
}

.live-search-page-player__name {
  font-weight: bold;
  font-size: 14px;
  margin-bottom: 0;
  white-space: nowrap;
  overflow: hidden;

  strong {
    color: #e70000;
  }
}

.live-search-page-player__flag {
  margin-left: 6px;
}

.live-search-page-team {
  cursor: pointer;
  position: relative;
  margin-bottom: 10px;
  background-color: #f2f2f2;
  color: #1a1a1a;
  border-radius: 6px;
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.3);

  &:hover {
    background-color: #dadada;
  }
}

.live-search-page-team__inner {
  padding: 18px 8px 8px 80px;
  height: 75px;
}

.live-search-page-team__image {
  position: absolute;
  bottom: 5px;
  left: 7px;
  width: 64px;
  height: 64px;
}

.live-search-page-team__name {
  font-weight: bold;
  font-size: 14px;
  margin-bottom: 0;

  strong {
    color: #e70000;
  }
}

.live-search-page-image {
  width: 64px;
  height: 64px;
}

.live-search-page-more-link {
  padding-left: 16px !important;
  padding-right: 16px !important;
}
