.map-block {
  display: flex;
  width: 100%;
  padding-top: 20px;

  &__filters {
    width: calc(100% - 380px);
    padding-left: 20px;
  }

  &__map-field {
    width: 380px;
  }

  &__filters-row {
    position: relative;
    margin-bottom: 10px;

    & > .custom-checkbox {
      font-weight: 700;
    }
  }

  &__title {
    font-weight: 700;
    position: relative;
    display: flex;
    min-height: 26px;
    line-height: 15px;
    cursor: pointer;
    text-overflow: ellipsis;
    overflow: hidden;
    &:after {
      position: absolute;
      top: 6px;
      right: 0px;
      flex-shrink: 0;
      content: '';
      width: 0;
      height: 0;
      border-left: 3px solid transparent;
      border-right: 3px solid transparent;
      border-top: 5px solid #000;
      margin-left: 5px;
      transition: ease-out 0.3s;
    }

    span {
      margin-bottom: 5px;
    }

    &_active {
      &:after {
        transform: rotateX(180deg);
        transition: ease-out 0.3s;
      }
    }
  }

  &__menu {
    position: absolute;
    min-width: 180px;
    z-index: 101;
    left: 0;
    display: flex;
    flex-direction: column;
    background-color: #fff;
    box-shadow: 0 0 15px rgba(0,0,0, 0.3);
    border-radius: 5px;
    padding: 10px 15px;

    &_open {
      display: flex;
    }
  }

  &__popup-zone-list {
    position: relative;
    list-style: none;
    margin: 0;
    padding: 0;
    
    display: flex;
    flex-wrap: wrap;
    
    width: 150px;
    margin-top: 5px;

    &::after {
      content: '';
      position: absolute;
      bottom: 0;
      left: -10px;
      width: 170px;
      height: 1px;
      background-color: #000;
    }

    &::before {
      content: '';
      position: absolute;
      bottom: -2px;
      left: -10px;
      width: 5px;
      height: 5px;
      border-radius: 50%;
      background-color: #000;

      box-shadow: 165px 0 #000;
    }

    &_home {
      &:after {
        content: '';
        position: absolute;
        left: -10px;
        top: 0;
        width: 170px;
        height: 1px;
        background-color: #000;
      }
      &::before {
        content: '';
        position: absolute;
        top: -2px;
        left: -10px;
        width: 5px;
        height: 5px;
        border-radius: 50%;
        background-color: #000;
  
        box-shadow: 165px 0 #000;
      }
    }
  }

  &__popup-zone {
    display: flex;
    align-items: center;
    justify-content: center;

    width: 50px;
    height: 50px;

    font-weight: 700;
    color: #999999;
    border-top: 1px solid #dddddd;
    border-left: 1px solid #dddddd;

    &:nth-child(4),
    &:nth-child(5),
    &:nth-child(6) {
      border-bottom: 1px solid #dddddd;
    }
    
    &:nth-child(3n) {
      border-right: 1px solid #dddddd;
    }

    cursor: pointer;

    &:hover,
    &_active {
      background-color: #dbf0ef;
    }
  }

  &__popup-button {
    display: flex;
    align-items: center;
    justify-content: center;

    width: 50px;
    height: 30px;
    margin: 0 auto;
    margin-top: 10px;

    font-size: 10px;
    color: #fff;
    font-weight: 700;
    text-transform: uppercase;

    border-radius: 5px;
    border: none;
    border-bottom: 1px solid #467171;
    background-image: linear-gradient(to bottom, #93c6c6, #6da0a0);

    outline: none;
    cursor: pointer;

    &:hover {
      background-image: linear-gradient(to bottom, darken(#93c6c6, 5%), darken(#6da0a0, 5%));
    }
  }

  &__controls {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 25px;
    height: 25px;

    .video__btn-container {
      position: absolute;
      top: 50%;
      left: 60%;
      transform: translate(-50%, -50%);
    }

    .players-details-header-episodes {
      display: flex;
      align-items: center;
      z-index: 100;
      font-size: 11px;
      line-height: 11px;
      letter-spacing: 1px;
      cursor: pointer;
  
      i {
        opacity: .5;
        margin-right: 5px;
      }
  
      &:hover {
        text-decoration: underline;
  
        i {
          opacity: 1;
        }
      }
    }
  
    .episodes-table-header-count__num {
      font-weight: 700;
  
      &_light {
        color: rgba(0, 0, 0, 0.5);
        padding: 0 0 0 5px;
      }
    }
  }

  &__controls-col {
    display: flex;
    align-items: center;

    >button:last-of-type {
      margin-right: 0;
    }
  }

  &__controls-button {
    display: flex;
    align-items: center;
    justify-content: center;

    width: 15px;
    height: 15px;
    margin-right: 15px;

    padding: 0;
    background: none;
    border: none;
    outline: none;
    cursor: pointer;

    i {
      filter: brightness(0);
      opacity: 0.6;
    }

    &__title {
      height: 15px;
    }

    .icon-select-action {
      background-image: url(/images/icons/svg/icon-action-hover.svg);
    }
    .icon-select-zone {
      background-image: url(/images/icons/svg/icon-zone-hover.svg);
    }
    .icon-area {
      background-image: url(/images/icons/svg/icon-area-hover.svg);
    }
    .icon-download {
      background-image: url(/images/icons/svg/icon-download-map-hover.svg);
    }

    &:hover,
    &_active {
      
      i {
        filter: brightness(1);
        opacity: 1;
      }
    }
  }

  &__settings {
    width: 16px;
    height: 16px;
    padding: 0;
    background: none;
    border: none;
    outline: none;
    cursor: pointer;

    .icon-settings {
      background-image: url('/images/icons/svg/icon-settings-hover.svg');
      filter: brightness(0);
      opacity: 0.6;
    }

    &:hover {
      .icon-settings {
        filter: brightness(1);
        opacity: 1;
      }
    }
  }

  &__select {
    flex-shrink: 0;
    position: relative;

    display: flex;
    align-items: center;

    .map-block__controls-button {
      width: 26px;

      &::after {
        flex-shrink: 0;
        content: '';
        width: 0;
        height: 0;
        border-left: 3px solid transparent;
        border-right: 3px solid transparent;
        border-top: 5px solid #000;
        margin-left: 5px;
        transition: ease-out 0.3s;
      }
    }

    &_open {

      .map-block__controls-button::after {
        transform: rotateX(180deg);
        transition: ease-out 0.3s;
      }
    }
  }

  &__select-list {
    position: absolute;
    z-index: 101;
    left: -10px;
    top: calc(100% + 5px);

    display: flex;
    flex-direction: column;

    display: flex;

    background-color: #fff;
    box-shadow: 0 0 15px rgba(0,0,0, 0.3);
    border-radius: 5px;
    padding: 5px 0;

    margin: 0;
    list-style: none;
  }

  &__select-item {
    white-space: nowrap;
    cursor: pointer;
    font-size: 11px;
    line-height: 24px;

    &:hover {
      background-color: #dbf0ef;
    }
  }

  &__map-field-inner {
    position: relative;
    width: 100%;
    height: 650px;

    img {
      width: 100%;
      height: auto;
    }
  }

  &__map-field-outer {
    padding: 20px 0;
    width: 100%;
    height: 695px;

    img {
      width: 100%;
      height: auto;
    }
  }

  &__map-field-zones {
    height: 645px;
  }

  &__zone-map {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
  }

  &__hover-zone {
    fill: rgba(255,255,255, 0.4);
    stroke: #2b8d8c;
    stroke-width: 0.5px;
    // cursor: pointer;

    &:hover {
      fill: rgba(255,255,255, 0.9);
    }
  }

  &__zones {

    &_basic-top,
    &_basic-bottom {
      .zone_7, .zone_8, .zone_9 {
        display: none;
      }
    }

    .zone_1 {
      top: 17%;
      left: 21%;
    }

    .zone_2 {
      top: 41%;
      left: 21%;
    }

    .zone_3 {
      top: 41%;
      left: 50%;
    }

    .zone_4 {
      top: 41%;
      left: 79%;
    }

    .zone_5 {
      top: 17%;
      left: 79%;
    }

    .zone_6 {
      top: 17%;
      left: 50%;
    }

    &_basic-bottom {
      .zone_1 {
        top: auto;
        bottom: 15%;
        left: 79%;
      }
  
      .zone_2 {
        top: auto;
        bottom: 38%;
        left: 79%;
      }
  
      .zone_3 {
        top: auto;
        bottom: 38%;
        left: 50%;
      }
  
      .zone_4 {
        top: auto;
        bottom: 38%;
        left: 21%;
      }
  
      .zone_5 {
        top: auto;
        bottom: 15%;
        left: 21%;
      }
  
      .zone_6 {
        top: auto;
        bottom: 15%;
        left: 50%;
      }
    }

    &_advanced-top {
      .zone_1 {
        top: 9%;
        left: 21%;
      }
  
      .zone_2 {
        top: 25%;
        left: 21%;
      }
  
      .zone_3 {
        top: 41%;
        left: 21%;
      }
  
      .zone_4 {
        top: 9%;
        left: 50%;
      }
  
      .zone_5 {
        top: 25%;
        left: 50%;
      }
  
      .zone_6 {
        top: 41%;
        left: 50%;
      }

      .zone_7 {
        top: 41%;
        left: 79%;
      }

      .zone_8 {
        top: 25%;
        left: 79%;
      }

      .zone_9 {
        top: 9%;
        left: 79%;
      }
    }

    &_advanced-bottom {
      .zone_1 {
        top: auto;
        bottom: 7%;
        left: 79%;
      }
  
      .zone_2 {
        top: auto;
        bottom: 23%;
        left: 79%;
      }
  
      .zone_3 {
        top: auto;
        bottom: 38%;
        left: 79%;
      }
  
      .zone_4 {
        top: auto;
        bottom: 7%;
        left: 50%;
      }
  
      .zone_5 {
        top: auto;
        bottom: 23%;
        left: 50%;
      }
  
      .zone_6 {
        top: auto;
        bottom: 38%;
        left: 50%;
      }

      .zone_7 {
        top: auto;
        bottom: 38%;
        left: 21%;
      }

      .zone_8 {
        top: auto;
        bottom: 23%;
        left: 21%;
      }

      .zone_9 {
        top: auto;
        bottom: 7%;
        left: 21%;
      }
    }
  }

  &__zones-episode {
    position: absolute;
    display: inline-flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transform: translate(-50%, -50%);
    z-index: 1;
    &:hover{
      cursor: pointer;
    }

    p {
      margin: 0;
      font-size: 21px;
      line-height: 26px;
      font-weight: 700;
    }
  }
}

.checked-list {
  position: relative;
  margin: 0;
  padding: 0;
  list-style: none;

  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;

  width: 100%;

  &__item {
    position: relative;
    display: flex;
    align-items: center;
    margin: 0 5px 3px 0;

    &:hover {

      .checked-list__item-name {
        color: rgba(26, 26, 26, 0.7);
      }

      .checked-list__item-remove {
        opacity: 0.7;
      }
    }
  }

  &__item-name {
    display: block;
  
    overflow: hidden;
    text-overflow: ellipsis;

    font-size: 11px;
    line-height: 11px;
    color: rgba(26, 26, 26, 0.5);
    padding-right: 2px;
    cursor: default;
  }

  &__item-remove {
    position: relative;
    display: block;
    width: 11px;
    height: 11px;
    cursor: pointer;
    opacity: .5;

    &::after,
    &::before {
      content: '';
      display: block;
      position: absolute;
      width: 9px;
      height: 1px;
      top: 50%;
      left: 50%;
      background-color: red;
    }

    &::before {
      transform: translate(-50%, -50%) rotate(45deg);
    }

    &::after {
      transform: translate(-50%, -50%) rotate(-45deg);
    }
  }
}