.match-header {
  display: flex;
  position: relative;
  min-height: 90px;
  box-shadow: 0 5px 5px rgba(38, 40, 44, 0.15);
  font-family: "PT Sans", Helvetica, Arial, sans-serif;
  color: #1a1a1a;
  background-color: #fff;

  &__container {
    display: flex;
    position: relative;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    padding: 10px 0;
  }

  &__main {
    display: flex;
    position: relative;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    padding: 0 20px;
    align-items: center;    
  }

  &__info {
    display: flex;
    position: absolute;
    justify-content: flex-end;
    align-items: center;
    width: 30%;
    right: 0;
    padding: 0 20px;
  }
  &__play {
    display: flex;
    position: relative;
    font-size: 11px;
    line-height: 1.5;

    &-button {
      position: relative;
      display: flex;
      align-items: center;
      height: 30px;
      padding-left: 35px;
      padding-right: 10px;
      white-space: nowrap;
      text-decoration: none;
      color: rgba(0, 0, 0, 0.5);
      cursor: pointer;

      &:hover {
        color: #1a1a1a;
        text-decoration: underline;
        &:before {
          opacity: 1;
        }
      }

      &:before {
        content: '';
        display: block;
        position: absolute;
        top: 50%;
        margin-top: -8px;
        left: 10px;
        width: 16px;
        height: 16px;
        opacity: 0.5;
        background-image: url(/images/icons/svg/icon-play.svg);
        background-size: contain;
      }

      &_xml {
        min-width: auto;
        padding-left: 10px;
        justify-content: center;
        &:before {
          display: none;
        }
      }
    }
  }

  &__event {
    color: rgba(0, 0, 0, 0.5);
  }

  &__teams {
    display: flex;
    position: relative;
    justify-content: center;
    align-items: center;
    margin: 0 0 5px 0;
    font-size: 24px;
    font-weight: 700;
    line-height: 27px;
    color:#1a1a1a;
    width: 70%;
  }

  &__score {
    position: absolute;
    display: block;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 120px;
    text-align: center;
  }

  &__periods-wrapper {
    margin-top: 5px;
  }

  &__periods {
    position: relative;
    padding: 1px 3px;
    background-color: #e6e6e6;
    margin-right: 20px;
    border-radius: 3px;
    &:last-child {
      margin-right: 0;
    }
  }

  &__home {
    padding-right: 60px;
    text-align: right;
    width: 50%;
  }
  &__away {
    padding-left: 60px;
    text-align: left;
    width: 50%;
  }
}
