.loader-wrapper {
 width: 200px;
 height: 200px;
 position: relative;
 margin: 0 auto;
 &:before {
   background: rgba(255, 255, 255, 0.85);
   content: '';
   left: 0;
   position: absolute;
   top: -25px;
   width: 100%;
   z-index: 100;
 }
 &:after{
   animation: three-quarters 1250ms infinite linear;
   border: 8px solid #A7A7A7;
   border-right-color: transparent;
   border-radius: 16px;
   box-sizing: border-box;
   content: '';
   display: inline-block;
   height: 32px;
   left: 50%;
   margin: -16px 0 0 -16px;
   overflow: hidden;
   position: absolute;
   text-indent: -9999px;
   top: 50%;
   width: 32px;
   z-index: 101;
 }
}

.loader-wrapper_cell {
  width: auto;
  height: auto;
  position: absolute;
  opacity: .4;
  left: 50%;
}
