
/*Video player*/
.video-popup{
  width: 850px;
  margin: 0 auto;
  position: relative;
  background-color: #1a1a1a;
  background-image: url("/images/video-player/logo.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 300px;
  .mfp-close{
    width: 17px;
    height: 20px;
    line-height: 28px;
    margin: 0;
    position: absolute;
    right: 0;
    top: -35px;
  }
}
.video-player-wrapper {
  background: #262626;
  border-right: 320px solid transparent;
  margin-bottom: 8px
}

.video-container {
  width: 850px;
  height: 480px;
  position: relative;
}

.video-title{
  margin: 0;
  font-size: 14px;
  color: #fff;
  position: absolute;
  top: -32px;
  left: 0;
  span{
    display: inline-block;
    margin-right: 20px;
    position: relative;
    &:after{
      content: "";
      display: block;
      width: 1px;
      height: 15px;
      background-color: #fff;
      position: absolute;
      right: -10px;
      top: 2px;
    }
    &:last-child:after{
      display: none;
    }
  }
}

.video-container .main-play-pause-control{
  width: 70px;
  height: 70px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  background: url("/images/video-player/icon-pause.svg") no-repeat;
  opacity: 0;
  cursor: pointer;
  transition: .3s;
  z-index: 100;
  &.pause{
    background: url("/images/video-player/icon-play.svg") no-repeat;
  }
}
.video-container:hover .main-play-pause-control{
  animation: videoFadeIn 5s;
  &:hover{
    opacity: 1;
  }
}

.video-container .video-controls {
  background: rgba(0, 0, 0, 0.8);
  bottom: 0;
  height: 56px;
  left: 0;
  opacity: 0;
  position: absolute;
  right: 0;
  transition: opacity 0.3s;
}

.video-container:hover .video-controls {
  opacity: 1;
}

.video-container .video-play {
  cursor: pointer;
  display: inline-block;
  height: 40px;
  left: 10px;
  position: absolute;
  top: 8px;
  width: 40px
}

.video-container .video-play i {
  left: 50%;
  margin: -10px 0 0 -10px;
  position: absolute;
  top: 50%
}

.video-container .video-play .icon-video-player-pause {
  display: block
}

.video-container .video-play .icon-video-player-play {
  display: none
}

.video-container .video-play.pause .icon-video-player-pause {
  display: none
}

.video-container .video-play.pause .icon-video-player-play {
  display: block
}

.video-container .video-mute {
  display: none
}

.video-container .video-next, .video-container .video-prev {
  display: none;
  width: 29px;
  height: 15px;
  position: absolute;
  top: 18px;
  left: 120px;
  background-image: url("/images/video-player/icon-video-next-prev.svg");
  cursor: pointer;
  &.inactive{
    opacity: .6;
    pointer-events: none;
  }
}
.video-container .video-prev {
  left: 75px;
  transform: rotate(180deg);
}
.video-container .video-counter{
  display: none;
  font-size: 14px;
  color: rgb(204,204,204);
  position: absolute;
  top: 16px;
  left: 165px;
  span{
    padding: 2px;
  }
}

.video-container .video-seek {
  background: #fff;
  bottom: 0;
  cursor: pointer;
  display: inline-block;
  height: 5px;
  left: 0;
  position: absolute;
  width: 100%
}

.video-container .video-seek input {
  display: none
}

.video-container .video-seek .video-seek-watched {
  background: #339595;
  bottom: 0;
  display: inline-block;
  height: 5px;
  left: 0;
  position: absolute
}

.video-container .video-full-screen {
  cursor: pointer;
  display: inline-block;
  height: 40px;
  right: 6px;
  position: absolute;
  top: 5px;
  width: 40px
}

.video-container .video-full-screen i {
  left: 50%;
  margin: -8px 0 0 -8px;
  position: absolute;
  top: 50%
}

.video-container .video-border {
  background: rgba(255, 255, 255, 0.4);
  height: 30px;
  position: absolute;
  top: 11px;
  width: 1px
}

.video-container .video-border.video-border_full-screen {
  right: 50px
}

.video-container .video-border.video-border_volume {
  right: 163px
}

.video-container .video-border.video-border_quality {
  right: 250px
}

.video-container .video-border.video-border_share {
  right: 295px
}

.video-container .video-volume {
  display: inline-block;
  position: absolute;
  right: 65px;
  top: 19px
}

.video-container .video-volume input {
  display: none
}

.video-container .video-volume .video-volume-item {
  border-left: 2px solid #a0a09f;
  cursor: pointer;
  display: inline-block;
  height: 14px;
  width: 2px;
}

.video-container .video-volume .video-volume-item.active {
  border-left: 2px solid #339595;
}

.video-container .video-add-playlist {
  cursor: pointer;
  display: inline-block;
  position: absolute;
  right: 326px;
  top: 19px
}

.video-container .video-quality-switcher {
  cursor: pointer;
  display: inline-block;
  position: absolute;
  right: 176px;
  top: 15px;
  width: 61px
}

.video-player-playlist {
  background: #262626;
  height: 344px;
  position: absolute;
  right: 0;
  top: 0;
  width: 320px
}

.video-player-playlist .video-playlist-header {
  border-bottom: 1px solid #393939;
  padding: 25px 40px 10px
}

.video-player-playlist .video-playlist-title {
  color: #fff;
  font-size: 18px;
  margin: 0
}

.video-player-playlist .video-playlist-subtitle {
  color: #808080;
  font-size: 11px
}

.video-player-playlist .video-playlist-info {
  color: #808080;
  font-size: 10px;
  margin: 5px 0
}

.video-player-playlist .video-playlist-views .icon-views {
  margin-top: 2px
}

.video-player-playlist .video-playlist-views-text {
  display: inline-block;
  padding: 2px 0 0 4px
}

.video-player-playlist .video-playlist-actions {
  padding: 20px 20px 10px 40px
}

.video-player-playlist .video-playlist-actions ul {
  margin: 0
}

.video-player-playlist .video-playlist-random {
  vertical-align: top
}

.video-player-playlist .video-playlist-repeat {
  display: inline-block;
  margin-left: 10px;
  margin-top: 1px;
  vertical-align: top
}

.video-player-playlist .video-playlist-share {
  color: #fff;
  float: right;
  font-size: 10px
}

.video-player-playlist .video-playlist-share .video-playlist-share-text {
  display: inline-block;
  margin-left: 7px;
  margin-top: 2px
}

.video-player-playlist .video-playlist-share:hover, .video-player-playlist .video-playlist-share:active,
.video-player-playlist .video-playlist-share:focus {
  text-decoration: none
}

.video-player-playlist .video-playlist-list {
  border-bottom: 1px solid #303030;
  display: inline-block;
  max-height: 168px;
  overflow: auto;
  position: relative;
  width: 100%
}

.video-player-playlist .video-playlist-list .video-playlist-item {
  border-top: 1px solid #303030;
  padding: 12px 40px;
  position: relative
}

.video-player-playlist .video-playlist-list .video-playlist-item:hover,
.video-player-playlist .video-playlist-list .video-playlist-item:active,
.video-player-playlist .video-playlist-list .video-playlist-item:focus,
.video-player-playlist .video-playlist-list .video-playlist-item.active {
  background: #303030
}

.video-player-playlist .video-playlist-list .video-playlist-item-title {
  color: #fff;
  font-size: 12px;
  margin: 0 0 3px 0
}

.video-player-playlist .video-playlist-list .video-playlist-item-subtitle {
  color: #808080;
  font-size: 11px;
  margin: 0
}

.video-player-playlist .video-playlist-list .video-playlist-item-link {
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%
}

.video-goal-header {
  border-bottom: 1px solid #e5e5e5;
  min-height: 126px
}

.video-goal-header-center {
  border-left: 120px solid transparent;
  border-right: 150px solid transparent
}

.video-player-image {
  left: 20px;
  position: absolute;
  top: 48px
}

.video-goal-scheme {
  position: absolute;
  right: 20px;
  top: 18px
}

.video-goal-title {
  color: #000;
  font-size: 24px;
  font-weight: bold;
  margin: 0 0 5px 0
}

.video-goal-title a {
  color: #000
}

.video-goal-info {
  color: #b2b2b2;
  font-size: 11px;
  margin-bottom: 5px
}

.video-goal-info a {
  color: #27aef1
}

.video-goal-info a:hover, .video-goal-info a:focus, .video-goal-info a:active {
  color: #27aef1
}

.video-goal-info .video-goal-info-item {
  display: inline-block;
  margin-right: 20px
}

.video-goal-system-info {
  color: #b2b2b2;
  font-size: 11px;
  margin: 0;
  padding: 10px 0px;
  width: 100%
}

.video-goal-system-info li {
  vertical-align: top
}

.video-goal-system-info li.video-goal-system-info_calendar {
  padding-top: 5px;
  width: 114px
}

.video-goal-system-info li.video-goal-system-info_views {
  padding-top: 5px;
  width: 180px
}

.video-goal-system-info li.video-goal-system-info_likes {
  padding-top: 5px;
  width: 138px
}

.video-goal-system-info li.video-goal-system-info_like {
  width: 145px
}

.video-goal-label .icon-views {
  margin-top: 3px
}

.video-goal-label .video-goal-label-text {
  display: inline-block;
  padding: 2px 0 2px 5px;
  vertical-align: top
}

.video-comments-title {
  color: #000000;
  font-size: 14px;
  font-weight: bold;
  margin: 0;
  padding: 18px 20px
}

.video-comment-form-container {
  background: #f8f8f8;
  border-bottom: 1px solid #e5e5e5;
  border-top: 1px solid #e5e5e5;
  display: inline-block;
  padding: 20px;
  width: 100%
}

.video-comment-form-container:after {
  clear: both;
  content: '';
  display: block
}

.video-comment-textarea-wrapper {
  display: inline-block;
  margin-left: 20px;
  margin-bottom: 10px;
  position: relative;
  width: 515px;
  vertical-align: top
}

.video-comment-textarea-wrapper:after {
  //background-image: url(http://gc.instatfootball.com/assets/comment-corner-9b32b0d456d5285552518574502f9c88.png);
  background-repeat: no-repeat;
  background-size: 14px;
  content: '';
  display: inline-block;
  height: 17px;
  left: -10px;
  position: absolute;
  top: 5px;
  width: 14px
}

.video-comment-textarea {
  background: #fff;
  border: 1px solid #e6e6e6;
  border-radius: 5px;
  height: 100px;
  width: 100%
}

.video-comments-list {
  padding: 10px 20px
}

.video-comment-item {
  border-bottom: 1px solid #e5e5e5;
  padding: 16px 0px 16px 60px;
  position: relative
}

.video-comment-item:last-child {
  border-bottom: none
}

.video-comment-item .video-comment-item-image {
  left: 0;
  position: absolute;
  top: 20px
}

.video-comment-item .video-comment-item-date {
  color: #b2b2b2;
  display: inline-block;
  font-size: 11px;
  margin-left: 10px
}

.video-comment-item .video-comment-item-text {
  margin: 3px 0
}

.video-external {
  cursor: pointer;
}

.icon-video-player-play {
  background-image: url('/images/video-player/icon-play.svg');
  background-position: 0 0;
  background-repeat: no-repeat;
  background-size: 15px;
  display: inline-block;
  height: 15px;
  vertical-align: top;
  width: 15px
}
.icon-video-player-pause {
  display: inline-block;
  height: 15px;
  position: absolute;
  vertical-align: top;
  width: 15px
}

.icon-video-player-pause:after, .icon-video-player-pause:before {
  background: #fff;
  content: '';
  display: inline-block;
  height: 15px;
  position: absolute;
  top: 0;
  width: 1px
}
.icon-video-player-pause:after {
  left: 2px
}

.icon-video-player-pause:before {
  right: 6px
}
.icon-video-player-screen {
  background-image: url('/images/icons/svg/icon-full-screen.svg');
  background-repeat: no-repeat;
  background-size: contain;
  display: inline-block;
  height: 18px;
  vertical-align: top;
  width: 18px
}
