.popup {
  left: 0;
  position: absolute;
  text-align: left;
  top: 35px;
  width: 100%;
  z-index: 10000;
  font: normal 12px / 17.1429px 'PT Sans', Tahoma, sans-serif;
  margin: 10px 0px 0px -29px;
  outline: rgb(255, 255, 255) none 0px;
  padding: 20px;
  height: auto;
  box-shadow: rgb(255, 255, 255) 0px 0px 7px 0px;
  box-sizing: border-box;
  color: rgb(255, 255, 255);
  cursor: default;
  perspective-origin: 350px 194px;
  transform-origin: 350px 194px;
  background: rgba(37, 37, 37, 0.952941) none repeat scroll 0% 0% / auto padding-box border-box;
  border: 0px none rgb(255, 255, 255);
  border-radius: 5px;

  .popup-col-title {
    margin: 25px 0 15px;
    color: white;
    font-size: 16px;
    text-align: left;
    display: block;
    font-weight: bold;

    @include laptop {
      margin: 10px 0;
    }
  }

  ul {
    box-sizing: border-box;
    color: rgb(255, 255, 255);
    cursor: default;
    text-align: left;
    width: 100%;
    height: auto;
    perspective-origin: 109.984px 163px;
    transform-origin: 109.984px 163px;
    border: 0px none rgb(255, 255, 255);
    list-style: none outside none;
    margin: 0px;
    outline: rgb(255, 255, 255) none 0px;
    padding: 0px;

    &:after {
      content: "";
      display: block;
      width: 100%;
      clear: both;
      height: 0;
    }
  }

  li {
    display: inline-block;
    box-sizing: border-box;
    white-space: nowrap;
    color: rgb(255, 255, 255);
    cursor: default;
    width: 100%;
    border: 0px none rgb(255, 255, 255);
    font: normal 12px / 17.1429px 'PT Sans', Tahoma, sans-serif;
    list-style: none outside none;
    margin: 0px 0px 4px;
    outline: rgb(255, 255, 255) none 0px;
    padding: 0px 15px 0 0;
    cursor: pointer;

    a {
      display: block;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }
  }

  #corner {
    top: -5px;
    box-sizing: border-box;
    color: rgb(255, 255, 255);
    cursor: default;
    height: 10px;
    left: 150px;
    position: absolute;
    text-align: left;
    width: 18px;
    perspective-origin: 9px 5px;
    transform-origin: 9px 5px;
    border-top: 0px none rgb(255, 255, 255);
    border-right: 9px solid rgba(0, 0, 0, 0);
    border-bottom: 10px solid rgba(37, 37, 37, 0.952941);
    border-left: 9px solid rgba(0, 0, 0, 0);
    font: normal 12px / 17.1429px 'PT Sans', Tahoma, sans-serif;
    outline: rgb(255, 255, 255) none 0px;
  }

  #popup-container {
    box-sizing: border-box;
    color: rgb(255, 255, 255);
    cursor: default;
    text-align: left;
    width: 100%;
    perspective-origin: 330px 174px;
    transform-origin: 330px 174px;
    border: 0px none rgb(255, 255, 255);
    font: normal 12px / 17.1429px 'PT Sans', Tahoma, sans-serif;
    outline: rgb(255, 255, 255) none 0px;
  }

  /*#DIV_3
  */

  .popup-col {
    box-sizing: border-box;
    color: rgb(255, 255, 255);
    cursor: default;
    float: left;
    height: auto;
    text-align: left;
    width: 20%;
    perspective-origin: 109.984px 163px;
    transform-origin: 109.984px 163px;
    border: 0px none rgb(255, 255, 255);
    font: normal 12px / 17.1429px 'PT Sans', Tahoma, sans-serif;
    outline: rgb(255, 255, 255) none 0px;

  }

  .active {
    box-sizing: border-box;
    color: rgb(255, 200, 33);
    display: block;
    height: 18px;
    position: relative;
    text-align: left;
    text-decoration: none;
    width: 209.984px;
    perspective-origin: 104.984px 9px;
    transform-origin: 104.984px 9px;
    border: 0px none rgb(255, 200, 33);
    font: normal 12px / 18px 'PT Sans', Tahoma, sans-serif;
    list-style: none outside none;
    outline: rgb(255, 200, 33) none 0px;
    padding: 0px 0px 0px 22px;
  }

  .active:before {
    box-sizing: border-box;
    color: rgb(255, 200, 33);
    content: '''';
    display: block;
    height: 16px;
    left: 0px;
    position: absolute;
    text-align: left;
    top: 1px;
    width: 16px;
    perspective-origin: 8px 8px;
    transform-origin: 8px 8px;
    background: rgba(0, 0, 0, 0) url("http://beta.instatscout.com/assets/checkbox_black-48700f56efc9c4d4d54c0a041db856d2.png") no-repeat scroll -18px 0px / auto padding-box border-box;
    border: 0px none rgb(255, 200, 33);
    font: normal 12px / 18px 'PT Sans', Tahoma, sans-serif;
    list-style: none outside none;
    outline: rgb(255, 200, 33) none 0px;
  }

  .noneactive {
    box-sizing: border-box;
    color: rgb(255, 255, 255);
    display: block;
    height: auto;
    position: relative;
    text-align: left;
    text-decoration: none;
    perspective-origin: 104.984px 9px;
    transform-origin: 104.984px 9px;
    border: 0px none rgb(255, 255, 255);
    font: normal 12px / 18px 'PT Sans', Tahoma, sans-serif;
    list-style: none outside none;
    outline: rgb(255, 255, 255) none 0px;
    padding: 0px 0px 0px 22px;
  }

  .noneactive:before {
    box-sizing: border-box;
    color: rgb(255, 255, 255);
    content: '''';
    display: block;
    height: 16px;
    left: 0px;
    position: absolute;
    text-align: left;
    top: 1px;
    width: 16px;
    perspective-origin: 8px 8px;
    transform-origin: 8px 8px;
    background: rgba(0, 0, 0, 0) url("http://beta.instatscout.com/assets/checkbox_black-48700f56efc9c4d4d54c0a041db856d2.png") no-repeat scroll 0% 0% / auto padding-box border-box;
    border: 0px none rgb(255, 255, 255);
    font: normal 12px / 18px 'PT Sans', Tahoma, sans-serif;
    list-style: none outside none;
    outline: rgb(255, 255, 255) none 0px;
  }
}

.popup.download {
  width: 150px;
  height: auto;
  left: 0;
  padding: 15px 10px 10px;
  margin: 10px 0px 0px 0px;
  transform: translateX(-10px);

  li:hover {
    color: #ffc821;
  }

  #corner {
    left: 25px;
  }
}

//for animate/////////////////////
.popup-modern-enter {
  opacity: 0;
}

.popup-modern-enter.example-enter-active {
  opacity: 1;
}

.popup-modern-leave {
  transform-origin: center;
}

.popup-modern-leave.popup-modern-leave-active {
  opacity: 0;
}



.popup-modern-enter .popup-inner {
  transform: translateY(0) scale(0, 0);
}

.popup-modern-enter.example-enter-active .popup-inner {
  transform: translateY(0) scale(1, 1);
  background-color: rgba(255, 255, 255, 0.7);
}

.popup-modern-leave .popup-inner {
  transform-origin: center;
}

.popup-modern-leave.popup-modern-leave-active .popup-inner {
  transform: translateY(0) scale(0, 0);
}

