.popup.popup-modern {
  display: flex;
  position: fixed;
  height: 100%;
  top: 0;
  margin: 0;
  width: 100vw;
  justify-content: center;
  background-color: rgba(255, 255, 255, 0.7);
  transform-origin: center;
  padding: 40px 20px 20px;

  @include laptop {
    padding: 20px;
  }

  #corner {
    display: none;
  }

  .popup-inner {
    display: flex;
    width: 90%;
    max-width: 1200px;
    max-height: max-content;
    max-height: -moz-max-content;
    flex-flow: row wrap;
    justify-content: space-between;
    background: #303030;
    padding: 0;
    overflow: auto;
    border-radius: 5px;
    transform-origin: center;
    transition: transform .3s;

    @include laptop {
      width: 95%;
      max-width: none;
    }
  }

  .popup-row-inner {
    display: flex;
    position: relative;
  }

  .popup-column-container {
    position: relative;
  }

  .popup-column-container.popup-column-container_choose-all-title {
    .popup-col-title {
      width: 50%;

      .checkbox-label {
        white-space: normal;
        overflow: visible;
        text-overflow: initial;

        &:before {
          top: 0;
          margin-top: 0;
        }
      }
    }

    .popup-col-title-choose-all {
      width: 40%;
      position: absolute;
      right: 0;
      top: 0;
      display: inline-block;
    }
  }

  .popup-row-header {
    display: flex;
    position: relative;
    width: 100%;
    flex-direction: column;
    align-items: center;
    border-bottom: 1px solid #505050;
    padding: 15px;

    @include laptop {
      padding: 10px;
    }

    .popup-title {
      display: flex;
      position: relative;
      width: auto;
      align-items: center;
      justify-content: center;
      margin: 0;
      font-weight: 700;
      font-size: 20px;
      line-height: 20px;
      margin-bottom: 5px;
      white-space: nowrap;

      &:hover {
        .popup-title__icon {
          display: flex;
        }

        input+.popup-title__icon {
          display: none;
        }
      }

      input {
        display: flex;
        position: relative;
        min-width: 300px;
        max-width: 700px;
        text-align: center;
        background: none;
        color: #fff;
        max-height: 20px;
        font-size: 20px;
        line-height: 11px;
        font-weight: 700;
        font-family: 'PT Sans', Tahoma, sans-serif;
        border: 1px solid rgba(193, 193, 193, 0.1);
      }

      &:after {
        content: "";
        display: inline-block;
        margin-left: 5px;
        position: relative;
        width: 12px;
        height: 12px;
        background-size: contain;
        background-repeat: no-repeat;
        opacity: 0;
      }

      &:hover:after {
        opacity: .7;
      }

      span:first-child {
        display: flex;
        position: relative;
        width: auto;
      }

      &__icon {
        display: none;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: -22px;
        width: 22px;
        height: 22px;
        background-image: url('/images/icons/svg/icon-edit_volleyball.svg');
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center;
      }
    }

    .popup-subtitle {
      display: flex;
      position: relative;
      width: auto;
      font-size: 12px;
      line-height: 12px;
      color: #aaa;
    }

    .popup-close {
      display: flex;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 25px;
      width: 20px;
      height: 20px;
      cursor: pointer;

      &:before {
        content: "";
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        width: 100%;
        height: 1px;
        background-color: rgba(255, 255, 255, 0.5);
        transform-origin: left;
        transform: rotate(-45deg) translate(-50%, -50%);
      }

      &:after {

        content: "";
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        width: 100%;
        height: 1px;
        background-color: rgba(255, 255, 255, 0.5);
        transform-origin: left;
        transform: rotate(45deg) translate(-50%, -50%);
      }
    }
  }

  .popup-col-title {

    .checkbox-label {
      display: flex;
      position: relative;
      white-space: normal;
      padding-left: 0;
      color: white;

      &:before {
        position: relative;
        min-width: 16px;
        top: auto;
        right: auto;
        left: auto;
        bottom: auto;
        margin: 0 5px 0 0;

      }

      span {}
    }
  }

  .popup-row-container {
    display: flex;
    position: relative;
    width: 74.333%;
    flex-wrap: wrap;
  }

  .popup-row-footer {
    display: flex;
    position: relative;
    justify-content: center;
    align-items: center;
    width: 100%;
    border-top: 1px solid #505050;
    padding: 15px;

    @include laptop {
      padding: 5px;
    }

    .popup-buttons {
      display: flex;
      position: relative;
      width: 100%;
      justify-content: center;
      align-items: center;

      input {
        display: flex;
        position: relative;
        min-width: 20%;
        height: 40px;
        justify-content: center;
        align-items: center;
        border: none;
        background: darken(#f7f7f7, 40%);
        background: linear-gradient(180deg, darken(#f7f7f7, 40%) 0, darken(#e0e0e0, 40%));
        border-radius: 5px;
        box-shadow: 0 1px 3px -1px rgba(0, 0, 0, .8);
        color: white;
        font-size: 13px;
        line-height: 13px;
        overflow: hidden;
        text-decoration: none;
        cursor: pointer;
        white-space: nowrap;

        @include laptop {
          height: 35px;
          min-width: 18%;
        }

        &:hover {
          background: darken(#f7f7f7, 50%);
          background: linear-gradient(180deg, darken(#f7f7f7, 50%) 0, darken(#e0e0e0, 50%));
        }
      }

      .popup-cancel {
        margin-right: 20px;
      }

      .popup-submit {
        text-transform: uppercase;
        font-weight: 700;
        background: linear-gradient(180deg, #339595 0, darken(#339595, 5%));

        &:hover {
          background: linear-gradient(180deg, darken(#339595, 5%) 0, darken(#339595, 10%));
        }
      }
    }
  }

  .popup-column-narrow {
    width: 25%;
    padding: 0 15px 20px;

    @include laptop {
      padding-bottom: 5px;
    }
  }

  .popup-column-medium {
    width: 20%;
    padding-right: 15px;
  }

  .popup-column-wide {
    width: 75%;
    padding: 0 15px 20px;

    &-table {
      display: flex;
      width: 100%;
      max-height: 300px;
      flex-direction: column;
      flex-wrap: wrap;

      &_low {
        max-height: 200px;
      }
    }
  }

  .popup-column-wide-table>.checkbox-wrapper {
    max-width: 180px;
    min-width: 150px;
    padding-right: 5px;
  }

  .popup-column-aside {
    display: flex;
    position: relative;
    flex-direction: column;
    width: 23.666%;
    background: #434343;
    padding-left: 15px;
    padding-right: 15px;

    .popup-col-title {
      opacity: 1;
      margin-bottom: 5px;
    }

    .checkbox-label {
      height: 25px;
      line-height: 25px;

      >span {
        margin-right: 15px;
        opacity: 0.7;
        color: white !important;
      }

      .short-input {
        border: 1px solid rgba(255, 255, 255, 0.2);
        background: linear-gradient(to bottom, rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.3));
        color: white;
        border-radius: 3px;
        padding: 3px;
        margin: 0 10px 0 7px;
        text-align: center;
        height: 21px;
      }

      &.checkbox-label_active {
        >span {
          opacity: 1;
          color: #ffc821 !important
        }
      }
    }

    .checkbox-label+.checkbox-label {
      color: rgba(255, 255, 255, 0.7);
    }

    .popup-duration-settings {
      display: flex;
      position: relative;
      width: auto;
      flex-direction: column;
      margin-top: auto;
      margin-bottom: 25px;

      @include laptop {
        margin-bottom: 10px;
      }

      .checkbox-label {
        display: flex;
        position: relative;
        width: auto;
        flex-direction: column;
        overflow: visible;
        height: auto;

        &:first-child {
          padding-left: 22px;

          &:before {
            top: 55%;
          }
        }

        span:nth-child(2) {
          padding-left: 20px;
        }
      }
    }
  }

  .popup-checkbox-table {
    display: flex;
    width: 100%;
    max-height: 360px;
    flex-direction: column;
    flex-wrap: wrap;
  }

  .popup-checkbox-tr {
    display: flex;
    flex-direction: column;
    width: 33.333%;
    margin-bottom: 15px;

    @include laptop {
      margin-bottom: 10px;
    }

    &:nth-child(4n+4) {
      margin-bottom: 0;
    }
  }

  .popup-checkbox-td {
    display: table-cell;
    padding-bottom: 4px;
    cursor: pointer;

    &:first-child {}

    &.popup-checkbox-td_title {
      width: 100%;
      padding-right: 10px;
    }

    &.popup-checkbox-td_with-shot {
      width: 100%;
      padding-right: 10px;
    }

    &.popup-checkbox-td_made {
      width: 100%;
      padding-right: 10px;
    }

    &.popup-checkbox-td_percent {
      width: 100%;
      padding-right: 10px;
    }
  }

  .popup-col-title.popup-col-title_narrow {
    width: 60%;
    display: inline-block;
  }

  .found-param-counter {
    margin-left: 10px;
    color: #ffcd00;
  }

  ul {
    margin-top: 15px;

    @include laptop {
      margin-top: 10px;
    }
  }

  .popup-col-comment {
    font: normal 1em "PT Sans";
    color: #aaa;
    margin-bottom: 15px;

    @include laptop {
      margin-bottom: 10px;
    }
  }

  .checkbox-wrapper {
    position: relative;
    height: 18px;
    margin-bottom: 4px;

    .checkbox-label-nums {
      opacity: 0;
      color: #ccc;
      font-size: 10px;
      padding-top: 1px;
      margin-left: 5px;
    }

    &:hover .checkbox-label-nums {
      opacity: 1;
    }
  }

  .checkbox-label {
    position: relative;
    padding-left: 22px;
    display: inline-block;
    max-width: 100%;
    vertical-align: top;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    color: rgba(white, .7);
    cursor: pointer;

    &:before {
      content: '';
      background: transparent url(/images/checkbox_black.png) 0 0 no-repeat;
      display: block;
      height: 16px;
      width: 16px;
      position: absolute;
      left: 0px;
      top: 50%;
      margin-top: -8px;
    }

    &.checkbox-label_active {
      color: #ffc821;

      &:before {
        background-position: -18px 0;
      }
    }
  }

  .checkbox-label-modal {
    position: absolute;
    left: -12px;
    top: -10px;
    padding: 10px 12px;
    background: #4d4d4d;
    box-shadow: 0 3px 5px rgba(0, 0, 0, 1);
    border-radius: 4px;
    z-index: 10;
    white-space: nowrap;

    .checkbox-small {
      font-size: 10px;
      padding-top: 2px;
      margin-left: 5px;
    }

    .checkbox-label {
      margin: 0 15px 0 0;

      .checkbox-small {
        color: white;
      }
    }

    .checkbox-label-ok {
      display: inline-block;
      margin-right: 15px;
      border: 1px solid rgba(255, 255, 255, 0.2);
      background: linear-gradient(to bottom, rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.3));
      color: white;
      border-radius: 3px;
      height: 17px;
      width: 21px;
      margin-top: 1px;
      text-align: center;
      font: normal 10px/17px "PT Sans";
    }

    .short-input {
      border: 1px solid rgba(255, 255, 255, 0.2);
      background: linear-gradient(to bottom, rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.3));
      color: white;
      border-radius: 3px;
      padding: 3px;
      margin: 0 7px 0 3px;
      text-align: center;
      height: 17px;
      width: 22px;
    }
  }


  .ul-checked-wrapper {
    max-height: 550px;
    overflow-y: hidden;

    @include laptop {
      max-height: 520px;
    }

    &:hover {
      overflow-y: auto;

      &::-webkit-scrollbar-button {
        width: 6px;
        height: 0
      }

      &::-webkit-scrollbar-track {
        border-radius: 5px;
        background-color: #32312e;
        box-shadow: 0 0 3px #000 inset;
      }

      &::-webkit-scrollbar-thumb {
        -webkit-border-radius: 5px;
        border: 1px solid rgba(#fff, .3);
        border-radius: 5px;
        background-color: rgba(37, 37, 37, 0.952941);
      }

      &::-webkit-resizer {
        width: 7px;
        height: 0
      }

      &::-webkit-scrollbar {
        width: 11px;
      }
    }

    ul {
      list-style: none;
      margin: 0;
      padding: 0;

      li {
        margin: 0;
        padding: 0;
        height: 30px;
        line-height: 30px;
        position: relative;
        border-bottom: 1px solid #515151;
        color: rgba(white, .7);

        @include laptop {
          height: 25px;
          display: flex;
          align-items: center;
        }

        .remover {
          width: 30px;
          height: 30px;
          position: absolute;
          right: 0;
          top: 0;
          text-align: center;
          line-height: 30px;
          color: #eee;
          font: normal 1.2em/30px Arial;
          opacity: 0.5;
          cursor: pointer;
          display: block;

          @include laptop {
            top: -2px;
          }
        }

        span {
          white-space: nowrap;
          overflow: hidden;
          display: block;
          width: 90%;
          text-overflow: ellipsis;
        }
      }
    }
  }


  &.popup-container_loading:before {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background: #303030;
    z-index: 10;
    border-radius: 5px;
  }

  &.popup-container_loading:after {
    content: "";
    display: block;
    position: absolute;
    left: 50%;
    top: 50%;
    margin-left: -100px;
    margin-top: -100px;
    width: 200px;
    height: 200px;
    z-index: 20;
    background: transparent url("/images/loader-large_basketball.png") no-repeat;
    background-size: 800px 200px;
    background-position: 0 0;
    animation: large-loader 1s steps(4) infinite;
  }

}