article.player {

  .player-card-left{
    display: flex;
    width: auto;
    align-self: flex-end;
    position: relative;
    height: 100%;
    align-items: center;

    &.player-card-left_loader{
      &:before{
        background: rgba(255, 255, 255, 0.85);
        content: '';
        height: 100%;
        left: 0;
        position: absolute;
        top: 0;
        width: 100%;
        z-index: 100;
      }
      &:after{
        animation: three-quarters 1250ms infinite linear;
        border: 8px solid #A7A7A7;
        border-right-color: transparent;
        border-radius: 16px;
        box-sizing: border-box;
        content: '';
        display: inline-block;
        height: 32px;
        left: 50%;
        margin: -16px 0 0 -16px;
        overflow: hidden;
        position: absolute;
        text-indent: -9999px;
        top: 50%;
        width: 32px;
        z-index: 101;
      }
    }
  }

  .player-card-right{
    min-width: 500px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
  }

  .player-card-responsive{
    border-width: 0;
    width: 200px;
    position: relative;
    align-self: center;

    .player-card-labels{
      margin-top: 0;
    }
  }

  .player-card-stats {
    display: inline-block;
    float: right;
    padding: 45px 20px 0 0;
    text-align: right;
    vertical-align: top;
    width: 100%;
    position: relative;
  }

  .player-card-wrapper{

    position: relative;

    .player-card-stats-dropdown {
      width: 100%;
      .selectBox-dropdown{
        border-color: #d2cfce;
        border-radius: 3px;
        color: #666;
        padding-bottom: 0;
        margin: 0;

        .selectBox-arrow{
          border-top-color: #666;
        }
      }
    }

    .player-card-stats-list{
      position: relative;
      display: flex;
      &.player-card-stats-list_loader{
        min-width: 500px;
        &:before{
          background: rgba(255, 255, 255, 0.85);
          content: '';
          height: 120px;
          left: 0;
          position: absolute;
          top: -25px;
          width: 100%;
          z-index: 100;
        }
        &:after{
          animation: three-quarters 1250ms infinite linear;
          border: 8px solid #A7A7A7;
          border-right-color: transparent;
          border-radius: 16px;
          box-sizing: border-box;
          content: '';
          display: inline-block;
          height: 32px;
          left: 50%;
          margin: -16px 0 0 -16px;
          overflow: hidden;
          position: absolute;
          text-indent: -9999px;
          top: 50%;
          width: 32px;
          z-index: 101;
        }
      }
      .player-cart-stats-item{
        width: auto;
        position: relative;
        text-align: center;
        display: inline-block;
        vertical-align: middle;
        color: black;
        padding: 0 15px;

        &.clickable {
          cursor: pointer;

          &:hover{
            .player-cart-stats-item-value span{
              border-bottom-color: black;
            }
          }

          &.tooltip-trigger {

            &:last-child {

              .hover-tooltip {
                left: 45%;

                &:before {
                  left: 54%;
                }
              }
            }
          }
        }
      }
      .player-cart-stats-item-label{
        position: relative;
        color: #999;
        font: normal 12px "PT Sans Narrow";
        width: 60px;
        height: 50px;
        text-transform: none;

        span {
          display: block;
          overflow: hidden;
        }
      }
      .player-cart-stats-item-value{
        font: 700 30px "PT Sans Narrow";

        span{
          border-bottom: 1px solid transparent;
          line-height: 32px;
          display: inline-block;
        }
      }
      .player-cart-stats-item.player-cart-stats-item_percent{
        .player-cart-stats-item-value:after{
          content: "%";
          display: inline-block;
          font-size: 17px;
          position: relative;
          top: -12px;
          color: #888;
        }
      }
    }
  }

}


@media screen and (max-width: 1570px) {
  article.player .player-card-wrapper .player-card-stats-list .player-cart-stats-item{
    padding: 0 10px;
  }
}

@media screen and (max-width: 1150px) {
  article.player .player-card-wrapper .player-card-stats-list .player-cart-stats-item{
    padding: 0 5px;
  }
  article.player .player-card-wrapper .player-card-stats-list .player-cart-stats-item.player-cart-stats-item_percent .player-cart-stats-item-value:after{
    float: right;
    position: static;
    margin-top: 5px;
  }
}


@for $i from 0 through 3 {
  $width: 1500 - $i * 70;
  @media screen and (max-width: #{$width}px) {
    article.player .player-card-wrapper .player-card-stats-list .player-cart-stats-item.player-cart-stats-item-max-width-#{$width}{
      display: none;
    }
  }
}
