.page-card {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 25px;
  background-color: #fff;
  height: 144px;

  &__left {
    display: flex;
    align-self: flex-end;
    position: relative;
    height: 100%;
    align-items: center;
  }

  &__avatar {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 120px;
    margin-bottom: 10px;
    margin-right: 10px;
    overflow: hidden;

    img {
      position: relative;
      z-index: 2;
      width: 90px;
      height: auto;
      margin-top: 24px;
    }
  }

  &__title {
    color: #1a1a1a;
    font-size: 18px;
    font-weight: bold;
    line-height: 18px;
    width: 320px;
    margin: 0;
    margin-bottom: 5px;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  &__info {
    font-size: 12px;
  }

  &__right {
  }

  &__stats {
    display: flex;
  }

  &__stats-item {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    color: #1a1a1a;
    padding: 0 15px;

    &.clickable {
      cursor: pointer;

      &:hover .page-card__stats-item-value {
        border-bottom: 1px solid #000;
      }
    }
  }

  &__stats-item-label {
    position: relative;
    color: #999;
    font: normal 12px "PT Sans Narrow";
    height: 20px;
    width: 60px;
    overflow: hidden;
    white-space: nowrap;
  }

  &__stats-item-value {
    font: 700 30px "PT Sans Narrow";
    line-height: 32px;

    &_percent {

      &:after {
        content: "%";
        display: inline-block;
        font-size: 17px;
        position: relative;
        top: -12px;
        color: #888;
      }
    }
  }

  &__responsive {
    width: 200px;
    position: relative;
  }

  &__dropdown {
    font-size: 12px;
    color: #1a1a1a;
    position: relative;

    .selectBox-dropdown {
      border-color: #d2cfce;
      border-radius: 3px;
      color: #666;
      padding-bottom: 0;
      margin: 0;
    }
  }
}
