.tournament-header-wrapper {
  display: flex;
  justify-content: space-between;
  height: 144px;
}

.tournament-header {
  background: white;
  padding: 0 13px;
  position: relative;

  &-left {
    display: flex;
    width: auto;
    align-self: flex-end;
    position: relative;
    height: 100%;
    align-items: center;
  }
  &__season-select {
    position: absolute;
    top: 65px;
    left: 50%;
    margin-left: -75px;
  }

  &__stats {
    min-width: 500px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
  }
}

.tournament-avatar {
  border-radius: 50%;
  overflow: hidden;
  background-color: #F2F2F2;
  height: 110px;
  width: 110px;
  display: flex;
  justify-content: center;
  align-items: center;
  vertical-align: unset;
  margin-bottom: 5px;
  margin-right: 20px;

    img {
      width: 100%;
    }
}