.dropdown-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  &:after {
    position: absolute;
    top: 40%;
    right: 10%;
    flex-shrink: 0;
    content: '';
    width: 0;
    height: 0;
    border-left: 3px solid transparent;
    border-right: 3px solid transparent;
    border-top: 5px solid #000;
    margin-left: 5px;
    transition: ease-out 0.3s;
  }

  &__opened { 
    &:after {
      transform: rotateX(180deg);
      transition: ease-out 0.3s;
    }
  }
}

.dropdown-control {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.dropdown-list {
  position: absolute;
  z-index: 101;
  top: calc(100% + 5px);
  display: flex;
  flex-direction: column;
  display: flex;
  background-color: #fff;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.3);
  border-radius: 5px;
  padding: 5px 0;
  margin: 0;
  list-style: none;

  &__hidden {
    display: none;
  }
}