.ui-button {
	background: #e9e9e9;
	border: 1px solid #fff;
	@include linearGradient(#f7f7f7, #e0e0e0);
	border-radius: 7px;
	box-shadow: 0 1px 3px -1px rgba(0,0,0,0.8);
	color: #000000;
	display: inline-block;
	font-size: 13px;
	line-height: 13px;
	overflow: hidden;
	padding: 9px 5px;
	text-align: left;
	text-decoration: none;
  cursor: pointer;

	span {
		padding-left: 4px;
		position: relative;
	}

	&.pushed {
		background: #d0d7d8;
		box-shadow: 0px -1px 0px 0px rgba(0,0,0,0.2), 0px 0px 1px 0px rgba(0,0,0,0.2);
	}
	&.disabled {
		@include opacity(0.5);
		&:hover {
			background: #e9e9e9;
			@include linearGradient(#f7f7f7, #e0e0e0);
			color: #000000;
			cursor: default;
		}
	}

	&:hover {
		background: #456164;
		@include linearGradient(#cccccc, #aaaaaa);
		color: #000000;
	}

	&:hover,
	&:focus,
	&:active {
		text-decoration: none;
	}

	&.ui-button-icon {
		height: 32px;
		text-align: center;
		width: 32px;
	}

	&.ui-button-best {
		@include linearGradient(#d4f7cf, #c1e1bd);

		&:hover {
			@include linearGradient(#c0f7b6, #aee0a4);
		}
	}
	&.ui-button-worst {
		@include linearGradient(#f7cbc9, #e0b8b6);

		&:hover {
			@include linearGradient(#f7b3b0, #e0a19f);
		}
	}
}

.ui-button__disabled{
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.3), inset 0 1px 0 rgba(0, 0, 0, 0);
    background: linear-gradient(to bottom, #EEEEEE 0%, #D1D1D1 100%);
    border-color: rgba(255,255,255,0.4);
	> *{
		opacity: 0.4;
	}
}

